<template>
    <div class='content-box'>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'>返回</span>
                    <span class="sign-in">钱包地址</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
          <qrcode-vue :value="qrCode" :size="size" level="H"></qrcode-vue>
          <p>钱包地址</p>
          <p   id="success_wallet_address">{{ qrCode }}</p>
          <button   ref="copy" data-clipboard-action="copy" data-clipboard-target="#success_wallet_address" @click="copyAddress">复制</button>
      </div>

      
    </div>
</template>

<script>
import axios from 'axios'
import QrcodeVue from 'qrcode.vue'

export default {
    name: 'walletAddress',
    components:{
        QrcodeVue
    },
    data () {
        return {
            size: 168,  //二维码尺寸大小
            qrCode: 'jfkasfmasfkljklshfuyasfhaskjfkasfmasfkljklshfuyasfhaskjbnfkjasghjbnfkjasgh', //钱包地址
        }
    },
    methods: {
        goBack(){
          this.$router.go(-1)
        },
        copyAddress(){ //复制钱包地址
          let _this = this;
          let clipboard = _this.copyBtn;
          clipboard.on('success', function() {
              layer.msg('复制成功')
          });
          clipboard.on('error', function() {
              layer.msg('复制失败')
          });
        },
        getAddress(){ //获取钱包地址
          const newData = ({
            queryid: this.queryid
          })
          axios.post(wallet_address,newData)
          .then(function(res){
            const data = res.data
            if(data.error == null){
              this.queryid = data.result.queryid
            }
          })
        }
    },
    mounted () {
      this.getAddress()
      this.copyBtn = new this.clipboard(this.$refs.copy);
    } 
}
</script>

<style scoped>
.content-box{
    min-height: 100vh;
  }
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 61%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}

.top-bg-box{
  width: 15rem;
  padding: 2rem 1rem;
  background-color: rgba(255,255,255);
  border-radius: 0.5rem;
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translate(-50%,-50%);
  text-align: center;
}
.top-bg-box:after{
  content: '';
  width: 3rem;
  height: 3rem;
  position: absolute;
  top: 0rem;
  left: 50%;
  transform: translate(-50%,-50%);
  background:url('../../assets/login/login-logo.png') no-repeat;
  background-size: cover;
}
.top-bg-box p{
  color: #0F76DF;
  font-size: 0.7rem;
  margin: 0.5rem 0;
  word-wrap: break-word;
  word-break: break-all;
  overflow: hidden;
}
.top-bg-box button{
  display: block;
  width: 4rem;
  height: 1.5rem;
  color: #0F76DF;
  font-size: 0.6rem;
  line-height: 1.5rem;
  text-align: center;
  background: #fff;
  border-radius: 0.4rem;
  border: 1px solid #0F76DF;
  outline: none;
  margin: 1rem auto 0rem auto;
}

</style>


