import Vue from 'vue'
import Router from 'vue-router'

import footer from '@/components/common/footer'

import login from '@/components/login'
import register from '@/components/register'

import forgetPassword from '@/components/pages/forgetpassword'
import home from '@/components/home'
import orderRecord from '@/components/orderrecord'
import community from '@/components/community'
import buyNode from '@/components/buynode'

import wallet from '@/components/pages/wallet'
import purchaseNode from '@/components/pages/purchasenode'
import turnOut from '@/components/pages/turnout'
import moneyRecord from '@/components/pages/moneyrecord'
import sellNode from '@/components/pages/sellnode'
import detailsList from '@/components/pages/detailslist'


import walletAddress from '@/components/wallet/walletaddress'
import activationCode from '@/components/wallet/activationcode'
import haveNode from '@/components/wallet/havenode'
import powPower from '@/components/wallet/powpower'
import transferRecord from '@/components/wallet/transferrecord'
import accountCenter from '@/components/wallet/accountcenter'
import paySetUp from '@/components/wallet/paysetup'
import bankSetUp from '@/components/wallet/banksetup'
import passwordSetUp from '@/components/wallet/passwordsetup'
import turnOutNode from '@/components/wallet/turnoutnode'
import loginPwdSetUp from '@/components/wallet/loginpwdsetup'
import wechatSetUp from '@/components/wallet/wechatsetup'


Vue.use(Router)

export default new Router({
  routes: [
    {
        path: '/login',
        name: 'login',
        components: {
            default: login
        },
        meta: {
            title: '登录',
            index: 0
        }
    },
    {
    	path:'/register',
    	name:'register',
    	components: {
            default: register
        },
        meta: {
            title: '注册',
            index: 1
        }
    },
    {
    	path:'/forgetpassword',
    	name:'forgetpassword',
    	components: {
            default: forgetPassword
        },
        meta: {
            title: '忘记密码',
            index: 2
        }
    },
    {
        path: '/',
        name: 'footer',
        components: {
            default: footer
        },
        redirect: '/home',
        children: [{
            path: 'home',
            name: 'home',
            component: home,
            meta: {
                title: '首页',
                index: 3
            }
        }]
    },
    {
    	path:'/orderrecord',
    	name:'orderrecord',
    	components: {
            default: orderRecord
        },
        meta: {
            title: '订单记录',
            index: 4
        }
    },
    {
        path:'/detailslist',
        name:'detailslist',
        component:detailsList,
        meta:{
            title:'申购详情',
            index: 4.5
        }
    },
    {
    	path:'/wallet',
    	name:'wallet',
    	components: {
            default: wallet
        },
        meta: {
            title: 'NB钱包',
            index: 5
        }
    },
    {
    	path:'/community',
    	name:'community',
    	components:{
    		default: community
    	},
    	meta:{
    		title:'我的社区',
            index: 6
    	}
    },
    {
    	path:'/buynode',
    	name:'buynode',
    	components:{
    		default:buyNode
    	},
    	meta:{
    		title:'购买',
            index: 7
    	}
    },
    {
    	path:'/purchasenode',
    	name:'purchasenode',
    	components:{
    		default:purchaseNode
    	},
    	meta:{
    		title:'申购节点',
            index: 8
    	}
    },
    {
        path:'/sellnode',
        name:'sellnode',
        components:{
            default:sellNode
        },
        meta:{
            title:'出售节点',
            index: 8.5
        }
    },
    {
    	path:'/turnout',
    	name:'turnout',
    	components:{
    		default:turnOut
    	},
    	meta:{
    		title:'转出NB',
            index: 9
    	}
    },
    {
    	path:'/moneyrecord',
    	name:'moneyrecord',
    	components:{
    		default:moneyRecord
    	},
    	meta:{
    		title:'资金记录',
            index: 10
    	}
    },
    {
    	path:'/walletaddress',
    	name:'walletaddress',
    	components:{
    		default:walletAddress
    	},
    	meta:{
    		title:'钱包地址',
            index: 11
    	}
    },
    {
    	path:'/activationcode',
    	name:'activationcode',
    	components:{
    		default:activationCode
    	},
    	meta:{
    		title:'激活码',
            index: 12
    	}
    },
    {
    	path:'/havenode',
    	name:'havenode',
    	components:{
    		default:haveNode
    	},
    	meta:{
    		title:'节点',
            index: 13
    	}
    },
    {
    	path:'/powpower',
    	name:'powpower',
    	components:{
    		default:powPower
    	},
    	meta:{
    		title:'POW算力',
            index: 14
    	}
    },
    {
    	path:'/transferrecord',
    	name:'transferrecord',
    	components:{
    		default:transferRecord
    	},
    	meta:{
    		title:'转让记录',
            index: 15
    	}
    },
    {
    	path:'/accountcenter',
    	name:'accountcenter',
    	components:{
    		default:accountCenter
    	},
    	meta:{
    		title:'账户信息管理',
            index: 16
    	}
    },
    {
    	path:'/paysetup',
    	name:'paysetup',
    	components:{
    		default:paySetUp
    	},
    	meta:{
    		title:'支付宝管理',
            index: 17
    	}
    },
    {
        path:'/wechatsetup',
        name:'wechatsetup',
        components:{
            default:wechatSetUp
        },
        meta:{
            title:'微信管理',
            index:17.5
        }
    },
    {
    	path:'/banksetup',
    	name:'banksetup',
    	components:{
    		default:bankSetUp
    	},
    	meta:{
    		title:'银行卡管理',
            index: 18
    	}
    },
    {
    	path:'/passwordsetup',
    	name:'passwordsetup',
    	components:{
    		default:passwordSetUp
    	},
    	meta:{
    		title:'修改二级密码',
            index: 19
    	}
    },
    {
    	path:'/turnoutnode',
    	name:'turnoutnode',
    	components:{
    		default:turnOutNode
    	},
    	meta:{
    		title:'节点种子转让',
            index: 20
    	}
    },
    {
        path:'/loginpwdsetup',
        name:'loginpwdsetup',
        components:{
            default: loginPwdSetUp
        },
        meta:{
            title: '修改登录密码',
            index: 21
        }
    }

  ]
})



