<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'>返回</span>
                    <span class="sign-in">银行卡管理</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
          <div class='setup-list'>
            <p>姓名</p>
            <input type="text" placeholder="请输入您的姓名" v-model='username'>
          </div>
          <div class='setup-list'>
            <p>开户行</p>
            <input type="text" placeholder="请输入您的开户行" v-model='bankname'>
          </div>
          <div class='setup-list'>
            <p>开户支行</p>
            <input type="text" placeholder="请输入您的开户支行" v-model='bankaddress'>
          </div>
         <!--  <div class='setup-list'>
            <p>手机号</p>
            <input type="text" placeholder="请输入银行预留手机号" v-model='mobile'>
          </div> -->
          <div class='setup-list'>
            <p>银行卡号</p>
            <input type="text" placeholder="请输入您的银行卡号" v-model='bankcard'>
          </div>
          <div class="code-list" v-show='flag'>
            <input type="text" placeholder="请输入验证码" v-model='codephone'>
            <p  @click="gainCodeFun"  >{{ codeText }}</p>
          </div>

          <button  @click='bankBtn'  v-show='flag'>保  存</button>
      </div>

     

    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'paySetUp',
    component:{
    
    },
    data () {
        return {
          username:'',//姓名
          bankname:'',//开户行
          // mobile:'',//手机号
          bankcard:'',//银行卡号
          codephone:'',//验证码
          type:'bank',
          bankaddress:'',//开户支行
          image:'',//支付宝微信付款码
          codeText: '发送验证码',
          codeClick: true,  //能否请求验证码接口
          allTime: 120,
          loading: '',//loading动画
          flag: true,//是否有初始数据
        }
    },
    methods: {
      goBack(){
        this.$router.go(-1)
      },
      countdown () {  //倒计时    
          this.time = setInterval(this.countdownTime,1000)
          this.countdownTime()
      },
      countdownTime () {
          this.codeText = this.allTime + 's后获取'
          if (this.allTime < 0) {
              clearInterval(this.time)
              this.codeText = '发送验证码'
              this.codeClick = true
              this.allTime = 120
              this.code=''
              return false
          }
          this.allTime--
      },
     gainCodeFun () { //获取短信验证码
          this.codeClick = false
          const NewData = ({
              'mobile':this.$store.state.mobile
          })
          this.$axios.post(binding_sms, NewData)
              .then(this.gainCodeSucc)
              .catch(this.gainCodeError)
      },
      gainCodeSucc (res) {
          const data = res.data
          if (data.result == true) {
              this.$layer.msg('验证码发送成功')
              this.countdown()
          } else {
              this.codeClick = true
              this.$layer.msg(data.error.msg)
          }
      },
      gainCodeError (error) {
          
      },
      bankBtn(){  //绑定银行卡
        if(this.username == ''){
          this.$layer.msg('请输入姓名')
        } else if (this.bankName == ''){
          this.$layer.msg('请输入开户行')
        } else if (this.bankaddress == ''){
          this.$layer.msg('请输入开户支行')
        } else if (this.bankcard == ''){
          this.$layer.msg('请输入银行卡号')
        } else if (this.codephone == ''){
          this.$layer.msg('请输入验证码')
        } else {
          const newData = ({
          "sms":this.codephone,
          "type":this.type,
          "name": this.username,
          "image":"",//支付宝微信付款码
          "number": this.bankcard,
          "bankname": this.bankname,
          "bankaddress": this.bankaddress,
          
          })
          this.loading = layer.load(2, {
                shade: [0.2,'#000',],//0.1透明度的黑色背景
                time: 3*1000
              })
          axios.post(pay_save,newData)
          .then(this.bankBtnSucc)
          .catch(this.bankBtnError)
        }
        
      },
      bankBtnSucc (res) {
          const data = res.data
          console.log(data.error == null)
          if (data.error == null) {
              layer.close(this.loading)
              this.$layer.msg('添加成功')
              this.$router.push({name: 'wallet'})
          } else {
              layer.close(this.loading)
              this.$layer.msg(data.error.msg)
          }
      },
      bankBtnError (error) {
           layer.close(this.loading)
      },
      getBankData(){  //获取已绑定的银行卡信息
        this.loading = layer.load(2, {
                shade: [0.2,'#000',],//0.1透明度的黑色背景
                time: 3*1000
              })
        const newData = ({
          "type":this.type
        })
        axios.post(alipay_data,newData)
        .then(this.getBankDataSucc)
        .catch(this.getBankDataError)
      },
      getBankDataSucc(res){
        const data = res.data
        if(data.error == null && data.result == null){
          this.flag = true
          layer.close(this.loading)
        }else if(data.error == null){
          this.username = data.result.realname,
          this.bankcard = data.result.number,
          this.bankname = data.result.bankname,
          this.bankaddress = data.result.bankaddress,
          this.flag = false,
          layer.close(this.loading)
        }else{
          layer.close(this.loading)
          this.flag = true
        }
      },
      getBankDataError(error){

      },
    },
    mounted () {
      this.getBankData()
    } 
}
</script>

<style scoped>
.top-bar {
  background: url(../../assets/wallet/nav-img.png) no-repeat;
  background-size: 100% 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 66%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}
.top-bar{

}
.top-bg-box{
  width: 100%;
  padding: 5rem 0.5rem 0rem 0.5rem;
}
.setup-list{
  height: 2rem;
  padding-left: 1rem;
  margin-bottom: 0.5rem;
  background:#fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.setup-list p{
  color: #828282;
  font-size: 0.8rem;
}
.setup-list input{
  width: 10rem;
  height: 2rem;
  text-align: right;
  font-size: 0.7rem;
  outline: none;
  padding-right: 1rem;
}
.code-list{
  height: 2rem;
  padding-left: 1rem;
  margin-bottom: 0.5rem;
  background:#fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.code-list input{
  width: 10rem;
  height: 2rem;
  text-align: left;
  font-size: 0.8rem;
  outline: none;
  /*padding-right: 1rem;*/
}
.code-list input:placeholder{
  font-size: 0.8rem;
}
.code-list p{
  color: #1D7AE0;
  font-size: 0.7rem;
  padding-right: 1rem;
}
.top-bg-box button{
  width: 13rem;
  height: 2rem;
  display: block;
  background: #1074E3;
  border-radius: 0.5rem;
  outline: none;
  margin: 2rem auto;
  font-size: 0.8rem;
  color: #fff;
}
</style>


