<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'>返回</span>
                    <span class="sign-in">修改二级密码</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
          <div class='setup-list'>
            <p>绑定手机号码</p>
            <input type="number" placeholder="请输入绑定手机号码" v-model='mobile'>
          </div>
          <div class='setup-list'>
            <p>新二级密码</p>
            <input type="password" placeholder="请输入新二级密码" v-model='password'>
          </div>
          <div class="code-list">
            <input type="number" placeholder="请输入验证码" v-model='codephone'>
            <p  @click="gainCode" >{{ codeText }}</p>
          </div>

          <button @click='pwdChangeBtn'>确   认</button>
      </div>

     

    </div>
</template>

<script>
import axios from 'axios'
import md5 from 'js-md5'

export default {
    name: 'passwordsetup',
    component:{
    
    },
    data () {
        return {
          mobile:'',//手机号码
          codephone:'', // 手机验证码
          password:'', // 新二级密码
          codeText: '发送验证码',
          codeClick: true,  //能否请求验证码接口
          allTime: 120,
          loading: '',//loading动画
        }
    },
    methods: {
      goBack(){
        this.$router.go(-1)
      },
      gainCode () { //点击获取验证码
          let codePwdReg = /(?!^(\d+|[a-zA-Z]+|[~!@#$%^&*?]+)$)^[\w~!@#$%^&*?]{6,18}$/
          let codeMobReg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/
          if (!this.mobile) {
              this.$layer.msg('请输入手机号')
              return false
          } else if (!codeMobReg.test(this.mobile)) {
               this.$layer.msg('请输入正确的手机号码')
              return false
          } else if (!this.password) {
              this.$layer.msg('请输入新二级密码')
              return false
          } else if (!codePwdReg.test(this.password)) {
              this.$layer.msg('请设置6到18位之间由数字、字母组合的密码')
              return false
          } else if (this.codeClick) {
              this.gainCodeFun()
              this.codeClick = false   
          }
      },
      countdown () {  //倒计时    
          this.time = setInterval(this.countdownTime,1000)
          this.countdownTime()
      },
      countdownTime () {
          this.codeText = this.allTime + 's后获取'
          if (this.allTime < 0) {
              clearInterval(this.time)
              this.codeText = '发送验证码'
              this.codeClick = true
              this.allTime = 120
              return false
          }
          this.allTime--
      },
     gainCodeFun () { //获取短信验证码
          const NewData = ({
              'mobile':this.mobile
          })
          this.$axios.post(changes_pwd_code, NewData)
              .then(this.gainCodeSucc)
              .catch(this.gainCodeError)
      },
      gainCodeSucc (res) {
          const data = res.data
          if (data.result == true) {
              this.$layer.msg('验证码发送成功')
              this.countdown()
          } else {
              this.codeClick = true
              this.$layer.msg(data.error.msg)
          }
      },
      gainCodeError (error) {
          
      },
      pwdChangeBtn(){  //修改二级密码
        if(this.codephone == ''){
          this.$layer.msg('请输入验证码')
        } else {
          const newData = ({
          "sms":this.codephone,
          "password":this.password
          })
          this.loading = layer.load(2, {
                shade: [0.2,'#000'],//0.1透明度的黑色背景
                time: 3*1000
              })
          axios.post(reg_pwd,newData)
          .then(this.pwdChangeSucc)
          .catch(this.pwdChangeError)
        }
        
      },
      pwdChangeSucc (res) {
          const data = res.data
          if (data.error == null) {
              layer.close(this.loading)
              this.$layer.msg('修改成功')
              this.$router.push({name: 'wallet'})
          } else {
              layer.close(this.loading)
              this.$layer.msg(data.error.msg)
          }
      },
      pwdChangeError (error) {
           layer.close(this.loading)
      },
    },
    mounted () {

    } 
}
</script>

<style scoped>
.top-bar {
  background: url(../../assets/wallet/nav-img.png) no-repeat;
  background-size: 100% 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 67%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}
.top-bar{

}
.top-bg-box{
  width: 100%;
  padding: 5rem 0.5rem 0rem 0.5rem;
}
.setup-list{
  height: 2rem;
  padding-left: 1rem;
  margin-bottom: 0.5rem;
  background:#fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.setup-list p{
  color: #828282;
  font-size: 0.8rem;
}
.setup-list input{
  width: 10rem;
  height: 2rem;
  text-align: right;
  font-size: 0.7rem;
  outline: none;
  padding-right: 1rem;
}
.code-list{
  height: 2rem;
  padding-left: 1rem;
  margin-bottom: 0.5rem;
  background:#fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.code-list input{
  width: 10rem;
  height: 2rem;
  text-align: left;
  font-size: 0.8rem;
  outline: none;
  /*padding-right: 1rem;*/
}
.code-list input:placeholder{
  font-size: 0.8rem;
}
.code-list p{
  color: #1D7AE0;
  font-size: 0.7rem;
  padding-right: 1rem;
}
.top-bg-box button{
  width: 13rem;
  height: 2rem;
  display: block;
  background: #1074E3;
  border-radius: 0.5rem;
  outline: none;
  margin: 2rem auto;
  font-size: 0.8rem;
  color: #fff;
}
</style>


