<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'>返回</span>
                    <span class="sign-in">N B 钱包</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
          <p>钱包地址:<br>
            <span  id="success_wallet_span">0x0FC68B263d0b8C7fE89E28aEfEf082Ae8aE9CD76</span>
          </p>
          <button  ref="copy" data-clipboard-action="copy" data-clipboard-target="#success_wallet_span" @click="copyLink">复   制</button>
      </div>

      <div class='grade-list'>
          <div class="right-line">
            <p>POS钱包</p>
            <p>0</p>   
          </div>
          <div class="right-line">
            <p>POW钱包</p>
            <p>0</p>   
          </div>
          <div>
            <p>节点种子<span>{{ nodeNum }}</span></p>
            <div class='sharing-links' @click='turnOutNode'>转让</div>   
          </div>
      </div>


      <div class='subscribe-list'>
          <router-link to="/walletaddress" class="purchase-node">
              <span>钱包地址</span>
              <div class="subscribe-list-right">
                <span></span>
              </div>  
          </router-link>       
          <router-link to="/activationcode" class='sale-node'>
              <span>激活码</span>
              <div class="subscribe-list-right">
                <span></span>
              </div>   
          </router-link>
          <router-link to="/havenode" class='turn-out'>
              <span>节点</span>
              <div class="subscribe-list-right">
                <span></span>
              </div>
          </router-link>
          <router-link to="/accountcenter"class='rush-buy'>
             <span>账户信息管理</span>
              <div class="subscribe-list-right">
                <span></span>
              </div> 
          </router-link>
          <router-link to="/transferrecord" class='turn-out'>
              <span>转让记录</span>
              <div class="subscribe-list-right">
                <span></span>
              </div> 
          </router-link>
          <router-link to="/powpower"class='rush-buy'>
             <span>POW算力</span>
              <div class="subscribe-list-right">
                <span></span>
              </div> 
          </router-link>
      </div>

    </div>
</template>

<script>
import axios from 'axios'
import { mapMutations } from 'vuex'

export default {
    name: 'wallet',
    component:{
    
    },
    data () {
        return {
           nodeNum:'0',//节点种子数量
           loading: '',//loading动画
        }
    },
    methods: {
        ...mapMutations(['AddNode']),
        goBack(){
          this.$router.push({name:'home'})
        },

        copyLink() {
          let _this = this;
          let clipboard = _this.copyBtn;
          clipboard.on('success', function() {
              layer.msg('复制成功')
          });
          clipboard.on('error', function() {
              layer.msg('复制失败')
          });
        },
        turnOutNode(){
          this.$router.push('/turnoutnode')
        },
        getProperty(){
          // this.loading = layer.load(2, {
          //       shade: [0.2,'#000',],//0.1透明度的黑色背景
          //       time: 3*1000
          //     })
          axios.post(wallet_property)
          .then(this.getPropertySucc)
          .catch(this.getPropertyError)
        },
        getPropertySucc(res){
          const data = res.data
          if(data.error == null){
            this.nodeNum = data.result.assets1
            this.AddNode(data.result.assets2)
            // layer.close(this.loading)
          }else{
            // layer.close(this.loading)
            this.$layer.msg(data.error.msg)
          }
        },
        getPropertyError(error){

        }

    },
    mounted () {
         this.copyBtn = new this.clipboard(this.$refs.copy);
         this.getProperty()
    } 
}
</script>

<style scoped>
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 60%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}

.top-bg-box{
  width: 100%;
  height: 13.6rem;
  background: url(../../assets/home/bg-images.png) no-repeat;
  background-size: 100% 100%;
  padding-top: 5rem;
}
.top-bg-box p{
  width: 17.527rem;
  margin: 0 auto;
  font-size: 0.8rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0.038rem;
  color: #ffffff;
}
.top-bg-box p span{
  word-break: normal; 
  width: auto; 
  display: block;
  white-space: pre-wrap;
  word-wrap: break-word;
  overflow: hidden;
}
.top-bg-box button{
  width: 7rem;
  height: 2rem;
  font-size: 0.8rem;
  outline: none;
  display: block;
  margin: 1rem auto;
  background-color: #ffffff;
  border-radius: 0.545rem;
}


.grade-list{
  width: 100%;
  height: 3.5rem;
  background-color: #fff;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.grade-list p:first-child{
  font-size: 0.8rem;
  margin-bottom: 0.3rem
}
.grade-list p:last-child{
  font-size: 0.5rem;
}
.right-line{
  position: relative;
}
.right-line:after{
  content: '';
  width: 1px;
  height: 2.5rem;
  background-color: #d0cfcf;
  position:absolute; 
  top: 0;
  right: -1.8rem;
}
.sharing-links{
  width: 3.2rem;
  height: 0.945rem;
  margin: 0 auto;
  background-color: #f76f3b;
  border-radius: 0.182rem;
  font-size: 0.6rem;
  line-height: 0.945rem;
}


.subscribe-list{
  width: 100%;
  height: auto;
  margin-top: 1rem;
  padding: 1rem 1rem;
  background: #fff;
}
.subscribe-list a {
  display: block;
  margin-bottom: 0.1rem;
  height: 2rem;
  line-height: 2rem;
  color: #000;
  font-size: 0.74rem;
  border-bottom: 1px solid #d0cfcf;
}
.sale-node span:nth-child(2){
  float: right;
  color: #ccc;
  margin-right: 0.2rem;
}
.turn-out span:nth-child(2){
  float: right;
  color: #ccc;
  margin-right: 0.2rem;
}
.sale-node span:nth-child(3){
  float: right;
}
.turn-out span:nth-child(3){
  float: right;
}
.subscribe-list a span:first-child{
  float: left;
}
.subscribe-list .subscribe-list-right{
  float: right;
}
.subscribe-list .subscribe-list-right span{
  position: relative;
}
.subscribe-list .subscribe-list-right span{
  display: block;
  width: 12rem;
  position: relative;
  text-align: right;
  padding-right: 1rem;
}
.subscribe-list .subscribe-list-right span:after {
  content: '';
  display: block;
  width: 10px;
  height: 10px;
  border-right: 1px solid #000;
  border-top: 1px solid #000;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  position: absolute;
  top: 0.7rem;
  right: 0.3rem;
}
</style>


