<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'>返回</span>
                    <span class="sign-in">激 活 码</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
          <div class='rule-box'>
            <p>使用规则:</p>
            <p>激活码用于社区会员激活账户所用，由上往下转让</p>
          </div>
      </div>
      
      <div class='buy-effect'>
        <div class='buy-list'>
          <span>转出数量：</span>
          <input type="number" name='' placeholder="请输入转出数量" v-model='num'>      
        </div>
        <div class='buy-list'>
          <span>转入账号：</span>
          <input type="number" name=''placeholder="请输入转入账号" v-model='mobile'>      
        </div>
        <button @click='goRecord'>提   交</button>
      </div>

      <div class='buy-effect' v-if="activation == ''">
        <p>您当前还没有激活码</p>   
      </div>
      <div class='buy-effect' v-else> 
        <ul class='activation-title'>
          <li>激活码</li>
          <li>状态</li>    
        </ul>
        <ul class='activation-list' v-for='(item,index) in activation' :key='index'>
          <li class='activation-code'>{{ item.code }}</li>
          <li v-if="item.is_use = 'N' " class='activation-use'>未使用</li>
          <li v-else-if="item.is_use = 'Y' "  class='activation-use'>已使用</li>
        </ul>
      </div>
     

    </div>
</template>

<script>
import axios from 'axios'


export default {
    name: 'activationCode',
    component:{
    
    },
    data () {
        return {
          num:'',//转出数量
          mobile:'',//转入账号
          activation:[],//激活码列表
        }
    },
    methods: {
        goBack(){
          this.$router.go(-1)
        },
        getActivation(){ //获取激活码列表
          this.loading = layer.load(2, {
              shade: [0.2,'#000'],//0.1透明度的黑色背景
                time: 3*1000
            })
          axios.post(wallet_activation)
          .then(this.getActivationSucc)
          .catch(this.getActivationError)
        },
        getActivationSucc(res){
          const data = res.data
          if(data.error == null){
            layer.close(this.loading)
            this.activation = data.result
          }else{
            layer.close(this.loading)
            this.$layer.msg(data.error.msg)
          }
        },
        getActivationError(error){
            layer.close(this.loading)
        },
        goRecord(){ //转出激活码请求
           if(this.num == ''){
              this.$layer.msg('请输入转出数量')
           } else if (this.mobile == '') {
              this.$layer.msg('请输入转入账号')
           } else {
              const newData = ({
                'num': this.num,
                'mobile':this.mobile
              })
              this.purchasenode(newData)
           }
        },
        purchasenode(newData){
          this.loading = layer.load(2, {
                shade: [0.2,'#000'] //0.1透明度的黑色背景
              })
          axios.post(activation_turn,newData)
          .then(this.purchasenodeSucc)
          .catch(this.purchasenodeError)
        },
        purchasenodeSucc(res){
          const data = res.data
          if (data.error == null) {
              layer.close(this.loading)
              this.$layer.msg('转出成功')
              this.num = ''
              this.mobile = ''
              this.$router.push({name:'wallet'})
          } else {
              layer.close(this.loading)
              this.num = ''
              this.mobile = ''
              this.$layer.msg(data.error.msg)
          }
        },
        purchasenodeError(error){
            layer.close(this.loading)
        },
    },
    mounted () {
        this.getActivation()
    } 
}
</script>

<style scoped>
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 60%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}

.top-bg-box{
  width: 100%;
  height: 13.6rem;
  background: url(../../assets/home/bg-images.png) no-repeat;
  background-size: 100% 100%;
  padding-top: 5rem;
  position: relative;
}
.rule-box{
  width: 15.727rem;
  height: 6.745rem;
  padding: 2rem 2.4rem;
  background-image: url('../../assets/wallet/line-rule.png');
  background-size: cover;  
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 0.5rem;
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.rule-box p{
  color: #fff;
  font-size: 0.7rem;
}

.buy-effect{
  width: 100%;
  height: auto;
  padding: 1rem 2rem;
  margin: 1rem 0;
  background: #fff;
}
.buy-effect p{
  color: red;
  font-size: 0.7rem;
  text-align: center;
}

.activation-title{
  width: 100%;
  height: 2rem;
  color: #000;
  font-size: 0.8rem;
  line-height: 2rem;
}
.activation-title li{
  float: left;
  width: 50%;
}
.activation-title li:last-child{
  float: right;
  width: 20%;
  text-align: center;
}
.activation-list{
  width: 100%;
  height: 2rem;
  color: #000;
  font-size: 0.7rem;
  line-height: 2rem;
  border-bottom: 1px solid #ccc;
}
.activation-list .activation-code{
  float: left;
  width: 50%;
}
.activation-list .activation-use{
  float: right;
  width: 20%;
  text-align: center;
}

.buy-effect{
  width: 100%;
  height: auto;
  padding: 1rem 2rem;
  margin-top: 1rem;
  background: #fff;
}
.buy-list{
  height: 2rem;
  line-height: 2rem;
  color: #000;
  font-size: 0.7rem;
  border-bottom: 1px solid #E5E5E5;
}
.buy-list input{
  outline: none;
  border:none;
  height: 2rem;
  border-bottom: 1px solid #E5E5E5;
}
.buy-list input::placeholder{
  color: #ccc;
}
.buy-list span{
  margin-right: 0.5rem;
}
.buy-effect button {
  display: block;
  width: 12.364rem;
  height: 1.818rem;
  background-color: #1075df;
  border-radius: 0.182rem;
  color: #fff;
  font-size: 0.8rem;
  line-height: 1.818rem;
  text-align: center;
  margin: 1rem auto;
  outline: none;
}
</style>


