<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'>返回</span>
                    <span class="sign-in">节  点</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
          <p>当前节点个数<span>{{ currentNode }}</span></p>
          <p>可释放NB<span>0</span></p>
      </div>

     

    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'haveNode',
    component:{
    
    },
    data () {
        return {
           currentNode:'',//当前节点个数
           loading: '',//loading动画
        }
    },
    methods: {
      goBack(){
        this.$router.go(-1)
      },
      getProperty(){
        this.loading = layer.load(2, {
                shade: [0.2,'#000',],//0.1透明度的黑色背景
                time: 3*1000
              })
        axios.post(wallet_property)
        .then(this.getPropertySucc)
        .catch(this.getPropertyError)
      },
      getPropertySucc(res){
        const data = res.data
        if(data.error == null){
          layer.close(this.loading)
          this.currentNode = data.result.assets2
        }else{
          layer.close(this.loading)
          this.$layer.msg(data.error.msg)
        }
      },
      getPropertyError(error){
        layer.close(this.loading)
      }
    },
    mounted () {
      this.getProperty()
    } 
}
</script>

<style scoped>
.top-bar {
  background: url(../../assets/wallet/nav-img.png) no-repeat;
  background-size: 100% 100%;
  /*position: fixed;
  top: 0;
  left: 0;*/
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 56%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}
.top-bar{

}
.top-bg-box{
  width: 100%;
  margin-top: 1rem;
  background: #fff;
  padding: 1rem 2rem;
}
.top-bg-box p:first-child{
  color: #000;
  font-size: 0.8rem;
  margin: 1rem 0;
}
.top-bg-box p:first-child span{
  margin-left: 1rem;
  color: red;
}
.top-bg-box p:last-child{
  color: #000;
  font-size: 0.8rem;
}
.top-bg-box p:last-child span{
  margin-left: 1rem;
  color: green;
}
</style>


