<template>
  <div id="app" :class="bgReg == true ? 'bg_reg':'bg_home'">       
    <transition :name="transitionName">
        <router-view></router-view>
    </transition>
  </div>
</template>

<script>
export default {
    name: 'App',
    data () {
        return {
            title: '首页',
            transitionName:''
      }
    },
    watch: {
        $route(to, from) { //如果to索引大于from索引,判断为前进状态,反之则为后退状态 
            document.title = this.$route.meta.title

            if(to.meta.index > from.meta.index){ //设置动画名称
                this.transitionName = 'slide-left'; 
            } else { 
                this.transitionName = 'slide-right'; 
            } 
        }

    },
    created() {
        // this.$router.push('/login')
    },
    computed: {
        bgReg () {
            let routerName = this.$route.name
            let showBg = routerName == 'login' || routerName == 'register' || routerName == 'forgetpassword' || routerName == 'walletaddress'
            if (showBg) {
                return true
            } else {
                return false
            }
        }
    },
    methods: {
        
    },
    mounted() {
        document.title = this.$route.meta.title
    }
}
</script>

<style scoped>
#app{
    min-height: 100vh;
}
.bg_reg {
    min-height: 100vh;
    background: url(./assets/login/bg-images.png) no-repeat;
    background-size: 100% 100%;
}
.bg_home{
  background: #EFEFEF;
}
.slide-right-enter-active, .slide-right-leave-active, .slide-left-enter-active, .slide-left-leave-active {
    will-change: transform;
    transition: all .3s;
    position: absolute;
    width:100%;
    top: 0;
    left:0;
} .slide-right-enter {
    transform: translateX(-100%);
} .slide-right-leave-active {
    transform: translateX(100%);
} .slide-left-enter {
    transform: translateX(100%);
} .slide-left-leave-active {
    transform: translateX(-100%);
}
</style>



