<template>
    <div class='content-box'>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='signOut'>退出登录</span>
                    <span class="sign-in">N B</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
          <p>新注册的用户需要激活码后才能正常使用，激活码
              可以向上级或上级的领导人索要或要求转让。被转
              让的激活码可以在NB钱包中查看，复制即可使用。</p>
          <button @click="activation" id='id' v-if='!ctivationFlag'>激   活</button>
          <button v-else>已激活</button>
      </div>

      <div class='grade-list'>
          <div class="nick-name">
            <p>{{ username }}</p>
            <p>昵称</p>   
          </div>
          <div class="invit-code">
            <p>邀请码<span>{{ invitcode }}</span></p>
            <p class='hidden-copy' id="success_form_span">{{ invitlink }}{{ invitcode }}</p>
            <div class='sharing-links' ref="copy" data-clipboard-action="copy" data-clipboard-target="#success_form_span" @click="copyLink">分享链接</div>   
          </div>
          <div class='vip-grade'>
            <p>VIP<span>{{ vipGrade }}</span></p>
            <p>等级</p>   
          </div>
      </div>

      <div class='wallet-box'>
          <router-link to="/wallet">
              <img src="../assets/home/wallet-img.png" alt="">
          </router-link>
      </div>

      <div class='subscribe-list'>
          <router-link to="/purchasenode" class="purchase-node">
              <img src="../assets/home/purchase-node.png" alt="">
              <span>申购节点</span>
          </router-link>
          <router-link to="/sellnode" class='sale-node'>
              <!-- <div @click='saleNode' class='sale-node'> -->
                <img src="../assets/home/sale-node.png" alt="">
                <span>出售节点</span>
              <!-- </div> -->
          </router-link>
          <router-link to="/turnout" class='turn-out'>
              <img src="../assets/home/turn-out.png" alt="">
              <span>转出NB</span>
          </router-link>
          <!-- <router-link to="" class='rush-buy'> -->
              <div @click='rushToBuy' class='rush-buy'>
                <img src="../assets/home/rush-buy.png" alt="">
                <span>抢购大厅</span>
              </div>        
          <!-- </router-link> -->
      </div>

    </div>
</template>

<script>
import axios from 'axios'
import { mapMutations } from 'vuex'


export default {
    name: 'home',
    component:{
    
    },
    data () {
        return {
          username: this.$store.state.nickname,//用户昵称
          copyBtn: null,
          invitcode:this.$store.state.invitcode,//邀请码
          invitlink:'http://localhost:8081/#/register?invitcode=',//分享链接
          vipGrade: this.$store.state.vipGrade,//vip等级
          sellNode: false,//出售节点权限
          rushBuy: false,//抢购大厅权限
          activationText:'激  活',
          ctivationFlag:false,//是否已激活
          queryid: '123456', //用户ID
        }
    },
    updated(){
        
    },
    methods: {
        ...mapMutations(['AddName']),
        ...mapMutations(['AddVip']),
        ...mapMutations(['AddInvit']),
        ...mapMutations(['clearUid']),


        
        getContent(){ //获取首页内容
          axios.post(hone_content)
          .then(this.getContentSucc)
          .catch(this.getContentError)
        },
        getContentSucc(res){
            const data = res.data
            if(data.error == null){
              this.username = data.result.nickname,
              this.invitcode = data.result.icode,
              this.vipGrade = data.result.level,
              this.ctivationFlag = data.result.isActive
              this.AddName(this.username)
              this.AddVip(this.vipGrade)
              this.AddInvit(this.invitcode)
            }
        },
        getContentError(error){

        },
        signOut(){ //退出登录
          axios.get(sign_out)
          .then(this.signOutSucc)
          .catch(this.signOutError)
        },
        signOutSucc(res){
            const data = res.data
            if(data.error == null){
              this.clearUid()
              layer.msg('退出成功')
              this.$router.push({name:'login'})
            } else {
              layer.msg(data.error.msg)
            }
        },
        signOutError(error){

        },
        // exit () { //退出登录
        //     this.$router.push({name:'login'})
        //     this.clearUid()
        // },
        activation(){  //激活码提交
          if(!this.ctivationFlag){
            layer.prompt({title: '请输入激活码', formType: 0}, function(val, index){
            const newData = ({
              'pin': val
            })
            // var loading = layer.load(2, {
            //     shade: [0.2,'#000'] 
            //   })
            axios.post(home_activation,newData)
            .then(function(res){
                  const data = res.data
                  if (data.error == null) {
                      layer.msg('已激活')
                      var parentId=parent.document.getElementById("id")
                      parentId.innerHTML = '已激活'
                      this.getContent()
                  } else {
                      layer.msg(data.error.msg)
                  }
                }
              )
              .catch()

              layer.close(index);
            });

          } else {

          }
          
        },

        saleNode(){
          if(!this.sellNode){
            layer.alert("新用户不能出售节点，请申购节点",{title:'提醒'})
          } else {
            this.$router.push({name: 'login'})
          }
          
        },
        rushToBuy(){
          if(!this.rushBuy){
            layer.alert("你没有进入抢购大厅的权限",{title:'提醒'})
          } else {
            this.$router.push({name: 'login'})
          }
          
        },
        copyLink() {
          let _this = this;
          let clipboard = _this.copyBtn;
          clipboard.on('success', function() {
              layer.msg('复制成功')
          });
          clipboard.on('error', function() {
              layer.msg('复制失败')
          });
        }
    },
    mounted () { 
      this.getContent()
      this.copyBtn = new this.clipboard(this.$refs.copy);
    }
}
</script>

<style scoped>
.content-box{
  min-height: 100vh;
}
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 55%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}

.top-bg-box{
  width: 100%;
  height: 13.6rem;
  background: url(../assets/home/bg-images.png) no-repeat;
  background-size: 100% 100%;
  padding-top: 5rem;
}
.top-bg-box p{
  width: 17.527rem;
  height: 2.509rem;
  margin: 0 auto;
  font-size: 0.7rem;
  font-weight: normal;
  font-stretch: normal;
  line-height: 0.873rem;
  letter-spacing: 0.038rem;
  color: #ffffff;
}
.top-bg-box button{
  width: 9.927rem;
  height: 2.364rem;
  font-size: 0.8rem;
  outline: none;
  display: block;
  margin: 1rem auto;
  background-color: #ffffff;
  border-radius: 0.545rem;
}


.grade-list{
  width: 100%;
  height: 3.5rem;
  background-color: #fff;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.grade-list p:first-child{
  font-size: 0.8rem;
  margin-bottom: 0.3rem
}
.grade-list p:last-child{
  font-size: 0.5rem;
}
.nick-name{
  width: 30%;
  height: 2rem;
  position: relative;
}
.invit-code{
  width: 45%;
  position: relative;
}
.vip-grade{
  width: 25%;
  height: 2rem;
  position: relative;
}
.invit-code:before{
  content: '';
  width: 1px;
  height: 2.5rem;
  background-color: #d0cfcf;
  position:absolute; 
  top: 0;
  left: 0rem;
}
.invit-code:after{
  content: '';
  width: 1px;
  height: 2.5rem;
  background-color: #d0cfcf;
  position:absolute; 
  top: 0;
  right: 0rem;
}

.sharing-links{
  width: 3.2rem;
  height: 0.945rem;
  margin: 0 auto;
  line-height: 0.945rem;
  background-color: #f76f3b;
  border-radius: 0.182rem;
  font-size: 0.6rem;
}

.wallet-box{
  width: 17.564rem;
  height: 2.964rem;
  border-radius: 0.727rem;
  margin: 1rem auto; 
}
.wallet-box img{
  width: 17.564rem;
  height: 2.964rem;
}

.subscribe-list{
  width: 100%;
  height: auto;
  padding: 0.5rem 0.2rem;
  background: #fff;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 5rem;
}
.subscribe-list a{
  width: 50%;
  height: 2.8rem;
  padding-left: 2rem;
  line-height: 2.8rem;
  font-size: 0.8rem;
  color: #000;
}
.subscribe-list div{
  width: 50%;
  height: 2.8rem;
  padding-left: 2rem;
  line-height: 2.8rem;
  font-size: 0.8rem;
  color: #000;
}
.subscribe-list a:first-child{
  border-right: 1px solid #d0cfcf;
  border-bottom: 1px solid #d0cfcf;
}
.subscribe-list .sale-node{
  border-bottom: 1px solid #d0cfcf;
}
.subscribe-list a:nth-child(3){
  border-right: 1px solid #d0cfcf;
}
.subscribe-list img{
  width: 1.891rem;
  height: 1.891rem;
}
.hidden-copy{
  color: #fff;
  height: 1px;
  overflow: hidden;
  border: 1px solid #fff;
  outline: none;
}
</style>


