<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'>返回</span>
                    <span class="sign-in">订单记录</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
         <div class='node-change'>
            <p v-for="(tab,index) in tabsName" @click="tabsSwitch(index)" v-bind:class="{active:tab.isActive}">{{ tab.name }}</p>
         </div>
         <div class='sale-node'>
           <p>当前出售节点个数:<span>{{ sellNodeNum[0] }}</span></p>
           <p>未完成订单个数:<span>{{ sellNodeNum[1] }}</span></p>
           <p>注：每笔订单需要在24小时内完成放行，超时会被冻
              结账户处理！</p>
         </div>
         <div class='sale-node'>
           <p>当前申购节点需求:<span>{{ sellNodeNum[2] }}</span></p>
           <p>未完成订单个数:<span>{{ sellNodeNum[3] }}</span></p>
           <p>注：每笔订单需要在24小时内完成打款，超时会被冻
              结账户处理！</p>
         </div>
      </div>
      
      <div class='record-list'>

        <p v-if="sellContent == ''">您当前还没有出售节点记录</p>

        <div class='sell-content' v-else>
          <ul class='sell-title'>
            <li class='width-one'>ID</li>
            <li class='width-two'>金额</li>
            <li class='width-three'>状态</li>
            <li class='width-four'>操作</li>
          </ul>
          <ul class='sell-list' v-for='(item,index) in sellContent' :key='index'>
            <li class='width-one'>{{ item.id }}</li>
            <li class='width-two'>{{ item.amount * nodePrice }} </li>
            <!-- 状态 -->
            <li class='width-three' v-if='item.status == 0'>已确认</li>
            <li class='width-three' v-else-if='item.status == 1'>待打款</li>
            <li class='width-three' v-else-if='item.status == 2'>待确认</li>
            <li class='width-three' v-else-if='item.status == 3'>未收到款</li>
            <li class='width-three' v-else>未知状态</li>
            <!-- 操作 -->
            <li class='width-four'  v-if='item.status == 2'>
              <button @click='voucherImg(item.config_image)'>查看凭证</button>
              <button @click='receivables(item.id)'>确认收款</button>
              <button @click='uncollected(item.id)'>未收到款</button>
            </li>
            <li class='width-four' v-else-if='item.status == 0'>已确认</li>
            <li class='width-four' v-else-if='item.status == 1'>待打款</li>
            <li class='width-four' v-else-if='item.status == 3'>未收到款</li>
            <li class='width-four' v-else>未知状态</li>

          </ul>
       
        </div>

      </div>


      <div class='record-list'>
        <p v-if="purchaseContent == ''">您当前还没有申购节点需求记录</p>
        
         <div class='sell-content2' v-else>
          <ul class='sell-title'>
            <li class='width-one2'>ID</li>
            <li class='width-two2'>金额</li>
            <li class='width-three2'>时间</li>
            <li class='width-four2'>状态</li>
          </ul>
            
          <ul class='sell-list' v-for='(item,index) in purchaseContent' :key='index'>
            <router-link :to="{path:'detailslist/', query:{orderid:item.id}}">
              <li class='width-one2'>{{ item.id }}</li>
              <li class='width-two2'>{{ item.amount * nodePrice }}</li>
              <li class='width-three2'>{{ item.time1 }}</li>
              <li class='width-four2' v-if='item.status == 0'>已确认</li>
              <li class='width-four2' v-else-if='item.status == 1'>待打款</li>
              <li class='width-four2' v-else-if='item.status == 2'>待确认</li>
              <li class='width-four2' v-else>未知状态</li>
            </router-link>
          </ul>
        </div>


      </div>
      

      
     

    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'orderRecord',
    component:{
    
    },
    data () {
        return {
          tabsName: [{
            name: "出售节点",
            isActive: true
          }, {
            name: "申购节点",
            isActive: false
          }],
          active: false,
          library_id:123, //选中查看记录ID
          sellContent:[],//出售内容列表
          purchaseContent:[],//申购内容列表
          loading: '',//loading动画
          nodePrice: 2000,//节点单价
        }
    },
    computed:{
        sellNodeNum:function(){ 
          var sellList = this.sellContent;
          var purchaselist = this.purchaseContent;
          var sellcount = 0;//当前出售节点个数
          var sellsumNum = 0;//出售未完成订单个数
          var purchasecount = 0;//当前申购节点个数
          var purchasesumNum = 0;//申购未完成订单个数
          var numArray = []
          for(let i = 0;i < sellList.length; i++){
            if(sellList[i].status == 1){
                sellcount += parseInt(sellList[i].amount);
            }
            if(!(sellList[i].status == 0)){
                sellsumNum += 1;
            }
          }
          for(let j = 0;j < purchaselist.length; j++){
            if(purchaselist[j].status == 1){
                purchasecount += parseInt(purchaselist[j].amount);
            }
            if(!(purchaselist[j].status == 0)){
                purchasesumNum += 1;
            }
          }
          numArray.push(sellcount,sellsumNum,purchasecount,purchasesumNum)
          return numArray
        },

    },
    methods: {
        goBack(){
          this.$router.push({name:'home'})
        },
        tabsSwitch: function(tabIndex) {  //出售与申购切换
          var tabCardCollection = document.querySelectorAll(".sale-node"),
          recordList = document.querySelectorAll(".record-list"),
            len = tabCardCollection.length;
 
          for(var i = 0; i < len; i++) {
            tabCardCollection[i].style.display = "none";
            recordList[i].style.display = 'none';
            this.tabsName[i].isActive = false;
          }
          this.tabsName[tabIndex].isActive = true;
          tabCardCollection[tabIndex].style.display = "block";
          recordList[tabIndex].style.display = "block";
        },
        getPurchaseContent(){ //获取申购节点列表
          axios.post(purchase_list)
          .then(this.getPurchaseContentSucc)
          .catch(this.getPurchaseContentError)
        },
        getPurchaseContentSucc(res){
          const data = res.data
          if(data.error == null){
            this.purchaseContent = data.result
          }else{
            this.$layer.msg(data.error.msg)
          }
        },
        getPurchaseContentError(error){

        },
        getSellContent(){ //获取出售节点列表
          this.loading = layer.load(2, {
                shade: [0.2,'#000',],//0.1透明度的黑色背景
                time: 3*1000
              })
          axios.post(sell_list)
          .then(this.getSellContentSucc)
          .catch(this.getSellContentError)
        },
        getSellContentSucc(res){
          const data = res.data
          if(data.error == null){
            this.sellContent = data.result
            this.sellContentFn(data.result)
            layer.close(this.loading)
          }else{
            layer.close(this.loading)
            this.$layer.msg(data.error.msg)
          }
        },
        getSellContentError(error){

        },
        sellContentFn(datas){
          /*let sellContentList = datas
          for(var i = 0;i < datas.length; i++){
            if(data[i].status == 1){
                var sellNodeNums += data[i].amount;
                console.log(sellNodeNum)
            }
          }*/
        },

        voucherImg(datas){  //查看凭证
          layer.open({
            type: 1,
            title:'打款凭证',
            // skin: 'layui-layer-rim', //加上边框
            area: ['350px', '540px'], //宽高
            content: "<img src='"+ url + datas + "' style='width:100%'>"
          })
        },
      
        receivables(userId){  //确认收款
          const newData = ({
              orderid:userId
          })
          axios.post(receivables,newData)
          .then(this.receivablesSucc)
          .catch(this.receivablesError)
        },
        receivablesSucc(res){
          const data = res.data
          if(data.error == null){
            layer.msg('确认成功')
            this.getSellContent()
          }else{
            layer.msg(data.error.msg)
          }
        },
        receivablesError(error){

        },
        uncollected(userId){  //未收到款
          const newData = ({
              orderid:userId
          })
          axios.post(uncollected,newData)
          .then(this.uncollectedSucc)
          .catch(this.uncollectedError)
        },
        uncollectedSucc(res){
          const data = res.data
          if(data.error == null){
            layer.msg('确认成功')
            this.getSellContent()
          }else{
            layer.msg(data.error.msg)
          }
        },
        uncollectedError(error){

        }
    },
    mounted () {
        this.getPurchaseContent()
        this.getSellContent()
    } 
}
</script>

<style scoped>
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 60%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}

.top-bg-box{
  width: 100%;
  height: 13.6rem;
  background: url(../assets/home/bg-images.png) no-repeat;
  background-size: 100% 100%;
  padding-top: 3.5rem;
  margin-bottom: 1rem;
}
.node-change{
  width: 100%;
  height: 2.5rem;
  /*position: fixed;
  top: 3.5rem;*/
  padding: 0 3.5rem;
  background: #fff;
  z-index: 33;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.node-change p{
  width: 35%;
  height: 1.5rem;
  text-align: center;
  color: #000;
  font-size: 0.74rem;
  position: relative;
  
}
.node-change p:first-child:after{
  content: '';
  width: 1px;
  height: 1.5rem;
  background-color: #d0cfcf;
  position:absolute; 
  top: -0.2rem;
  right: -2rem;
}
.active{
  color: #1075df!important;
  border-bottom: 1px solid #1075df;
}
.sale-node{
  width: 100%;
  padding: 1rem 2rem 0 2rem;
}
.sale-node ~ .sale-node{
  display: none;
}
.record-list ~ .record-list{
  display: none;
}
.sale-node p{
  color: #fff;
  font-size: 0.7rem;
  line-height: 1.5rem;
}
.sale-node p span{
  margin-left: 0.5rem;
}
.sale-node p:last-child{
  color: #fff;
  font-size: 0.6rem;
  line-height: 1rem;
  margin-top: 1rem;
}
.record-list{
  width: 100%;
  height: auto;
  background: #fff;
  padding: 1rem 0;
  margin-bottom: 1rem;
    overflow-x: scroll;
}
.record-list p{
  color: red;
  font-size: 0.7rem;
  text-align: center;
}

.sell-content{
  width: 130%;
  height: auto;
  background: #fff;
  overflow-x: scroll;
}
.sell-content2{
  width: 100%;
  height: auto;
  background: #fff;
  overflow-x: scroll;
}
.sell-title{
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  font-size: 0.7rem;
  color: #000;
  text-align: center;
  border-bottom: 1px solid #ccc;
}
.sell-title li{
  float: left;
}
.width-one{
  width: 15%;
}
.width-two{
  width: 25%;
}
.width-three{
  width: 15%;
}
.width-four{
  width: 45%;
}
.width-one2{
  width: 15%;
}
.width-two2{
  width: 25%;
}
.width-three2{
  width: 40%;
}
.width-four2{
  width: 20%;
}
.sell-list{
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  font-size: 0.6rem;
  color: #000;
  text-align: center;
  border-bottom: 1px solid #ccc;
}
.sell-list li{
  float: left;
  text-align: center;
}
.width-four button{
  width: 3rem;
  height: 1.5rem;
  line-height: 1.5rem;
  outline: none;
  color: #fff;
  border-radius: 0.2rem;
  background: #3698ff;
}
.width-four button:nth-child(2){
  background: #10dfd7;
}
.width-four button:last-child{
  background: #f2583e;
}
</style>


