<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'>返回</span>
                    <span class="sign-in">申购详情</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
          <div class='paytype-box'>

            <ul class='paytype-alipay'>
              <li>
                <h3>订单详情</h3>
              </li>
              <li>
                <span>支付金额:</span>
                <span>{{ orderdetails.amount }}</span>
              </li>
            </ul>

            <div v-for='(item,index) in detailsData' :key='item.index' v-show='detailsData.length > 0'> 
              <ul class='paytype-alipay' v-if="item.type == 'alipay' ">
                <li>
                  <h3>支付宝支付</h3>
                </li>
                <li>
                  <span>姓名:</span>
                  <span>{{ item.realname }}</span>
                </li>
                <li>
                  <span>账号:</span>
                  <span>{{ item.number }}</span>
                </li>
                <li>
                  <span>二维码:</span>
                  <span><img :src="'http://www.hz20181008.cn' + item.image" alt=""></span>
                </li>
              </ul>

              <ul class='paytype-wechat' v-if="item.type == 'wechat' ">
                <li>
                  <h3>微信支付</h3>
                </li>
                <li>
                  <span>姓名:</span>
                  <span>{{ item.realname }}</span>
                </li>
                <li>
                  <span>账号:</span>
                  <span>{{ item.number }}</span>
                </li>
                <li>
                  <span>二维码:</span>
                  <span><img :src="'http://www.hz20181008.cn' + item.image" alt=""></span>
                </li>
              </ul>

              <ul class='paytype-bank' v-if="item.type == 'bank' ">
                <li>
                  <h3>银行卡支付</h3>
                </li>
                <li>
                  <span>姓名:</span>
                  <span>{{ item.realname }}</span>
                </li>
                <li>
                  <span>开户行:</span>
                  <span>{{ item.bankname }}</span>
                </li>
                <li>
                  <span>开户支行:</span>
                  <span>{{ item.bankaddress }}</span>
                </li>
                <li>
                  <span>银行卡号:</span>
                  <span>{{ item.number }}</span>
                </li>
              </ul>
            </div>

          </div>
      </div>

      <div  class='top-bg-box'>
        <div class='paytype-box'>
            <h3>上传打款截图</h3>
            <div class='updata-img' >
              <img src="../../assets/wallet/remittance.png" alt="" id="portrait" v-if='flag'>
              <img :src="this.configImage" alt="" id="portrait" v-else>
              <input type="file" id="saveImage" name="myphoto" @change="tirggerFile($event)">
            </div>

            <button @click='uploadsdetails'>{{ btnTxt }}</button>

        </div>
      </div>

     

    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'detailsList',
    component:{
    
    },
    data () {
        return {
           orderid:this.$route.query.orderid,//对应id
           orderdetails:[],//订单详情
           detailsData:[],//支付方式
           btnTxt: '上传凭证',
           loading: '',//loading动画
           configImage:'',
           flag: true,//是否有初始数据
           image:'',//上传图片地址
           userid:''
        }
    },
    methods: {
        goBack(){
          this.$router.go(-1)
        },
        getdetailsContent(orderid){  //获取订单支付方式详情
          const newData = ({
            orderid:this.orderid
          })
          axios.post(purchase_details_list,newData)
          .then(this.getdetailsContentSucc)
          .catch(this.getdetailsContentError)
        },
        getdetailsContentSucc(res){
          const data = res.data
          if(data.error == null){
            this.orderdetails = data.result.order,
            this.detailsData = data.result.pays
            this.userid = data.result.order.userid
            if(this.orderdetails.config_image != ''){
              // this.configImage = 'http://www.hz20181008.cn' + this.orderdetails.config_image
              this.configImage =  'http://www.hz20181008.cn' + data.result.order.config_image
              this.flag = false;
            }
          }else{
            layer.msg(data.error.msg)
          }
        },
        getdetailsContentError(error){

        },
        tirggerFile(event){  //图片提交
            console.log(event)
            var file = event.target.files[0];
            let param = new FormData(); //创建form对象
            param.append('file',file);//通过append向form对象添加数据
            let config = {
              headers:{'Content-Type':'multipart/form-data'}
            }; //添加请求头
            this.updataImg(param,config)
        },
        updataImg(param,config){
          axios.post(uploads_transfer,param,config)
          .then(this.updataImgSucc)
          .catch(this.updataImgError)
        },
        updataImgSucc(res){
          const data = res.data
          if(data.error == null){
            this.configImage = data.result
          }else{
            this.$layer.msg(data.error.msg)
          }
        },
        updataImgError(error){
        },
        great(){  //实时显示该图片在页面
            document.getElementById('saveImage').onchange = function () {
            var imgFile = this.files[0];
            var fr = new FileReader();
            fr.onload = function () {
              document.getElementById('portrait').src = fr.result;
            };
            fr.readAsDataURL(imgFile);
            } 
        },


        uploadsdetails(orderid){ //凭证图片提交
          this.loading = layer.load(2, {
                shade: [0.2,'#000'],//0.1透明度的黑色背景
                time: 3*1000
              })
          const newData = ({
            orderid: this.orderid,
            image: this.configImage
          })
          axios.post(transfer_image,newData)
          .then(this.uploadsdetailsSucc)
          .catch(this.uploadsdetailsError)
        },
        uploadsdetailsSucc(res){
          const data = res.data
          if(data.error == null){
            layer.msg('上传成功')
            layer.close(this.loading)
            this.$router.push({name:'orderrecord'})
          }else{
            layer.close(this.loading)
            layer.msg(data.error.msg)
          }
        },
        uploadsdetailsError(error){
          layer.close(this.loading)
        },
    },
    mounted () {
      this.getdetailsContent()
      this.great()
    } 
}
</script>

<style scoped>
.top-bar {
  background: url(../../assets/wallet/nav-img.png) no-repeat;
  background-size: 100% 100%;
/*  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;*/
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 62%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}
.top-bar{

}
.top-bg-box{
  width: 100%;
  background: #fff;
  margin-top: 1rem;
  padding: 1rem 1rem;
  margin-bottom: 2rem;
}
.paytype-box{
  width: 100%;
}
/*.paytype-box h3{
  margin-bottom: 0.5rem;
}*/
.paytype-alipay{
  width: 100%;
  height: auto;
  padding: 1rem;
  border: 1px solid #ccc;
  border-radius: 0.5rem;
  margin-bottom: 0.5rem;
}
.paytype-alipay li{
  width: 100%;
  color: #000;
  font-size: 0.7rem;
  padding: 0.5rem 0;
}
.paytype-alipay li span{
  width: 100%;
  color: #000;
  font-size: 0.7rem;
}
.paytype-alipay li img{
  border: 1px solid #ccc;
  height: 7rem;
}
.paytype-wechat{
  width: 100%;
  height: auto;
  padding: 1rem;
  border: 1px solid #ccc;
  border-radius: 0.5rem;
  margin-bottom: 0.5rem;
}
.paytype-wechat li{
  width: 100%;
  color: #000;
  font-size: 0.7rem;
  padding: 0.5rem 0;
}
.paytype-wechat li span{
  width: 100%;
  color: #000;
  font-size: 0.7rem;
}
.paytype-wechat li img{
  border: 1px solid #ccc;
  height: 7rem;
}
.paytype-bank{
  width: 100%;
  height: auto;
  padding: 1rem;
  border: 1px solid #ccc;
  margin-bottom: 0.5rem;
  border-radius: 0.5rem;
}
.paytype-bank li{
  width: 100%;
  color: #000;
  font-size: 0.7rem;
  padding: 0.5rem 0;
}
.paytype-bank li span{
  width: 100%;
  color: #000;
  font-size: 0.7rem;
}
.paytype-bank li img{
  border: 1px solid #ccc;
  height: 7rem;
}


/*
.updata-img{
  width: 8rem;
  height: 8rem;
  border:1px solid #ccc;
  margin: 1rem auto;
}
.updata-img img{
  width: 8rem;
  height: 8rem;
}*/
.paytype-box button{
  width: 13rem;
  height: 2rem;
  display: block;
  background: #1075df;
  color: #fff;
  font-size: 0.7rem;
  line-height: 2rem;
  text-align: center;
  margin: 1rem auto;
  border-radius: 0.4rem; 
  outline: none;
}
.updata-img{
  width: 10rem;
  height: 10rem;
  padding: 0.5rem;
  background: #fff;
  border: 1px solid #ccc;
  margin: 2rem auto;
  position: relative;

}
.updata-img img{
  position: absolute;
  top: 0;
  left: 0;
  width: 10rem;
  height: 10rem;
}
.top-bg-box .updata-transfer{
  width: 13rem;
  height: 2rem;
  display: block;
  background: #1074E3;
  border-radius: 0.5rem;
  outline: none;
  margin: 2rem auto;
  font-size: 0.8rem;
  line-height: 2rem;
  text-align: center;
  color: #fff;
  position: relative;
}
#saveImage{
  position: absolute;
  top: 0rem;
  left: 0rem;
  width: 10rem;
  height: 10rem;
  opacity: 0;
}
</style>


