<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'>返回</span>
                    <span class="sign-in">资金记录</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
          <p>您当前还没有资金记录</p>
      </div>

     

    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'wallet',
    component:{
    
    },
    data () {
        return {
           
        }
    },
    methods: {
      goBack(){
          this.$router.go(-1)
        },
    },
    mounted () {

    } 
}
</script>

<style scoped>
.top-bar {
  background: url(../../assets/wallet/nav-img.png) no-repeat;
  background-size: 100% 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 60%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}
.top-bar{

}
.top-bg-box{
  width: 100%;
  height: 3.6rem;
  padding-top: 5rem;
}
.top-bg-box p{
  color: red;
  font-size: 0.7rem;
  text-align: center;
}

</style>


