<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'>返回</span>
                    <span class="sign-in">POW算力</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
         <div class='wallet-pow'>
           <p>POW钱包:<span>0</span></p>
         </div>  
         <div class='power-pow'>
           <p>可释放POW算力:<span>0</span></p>
           <button @click='powPower'>释放</button>
         </div>  
      </div> 

    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'powPower',
    component:{
    
    },
    data () {
        return {
           
        }
    },
    methods: {
        goBack(){
          this.$router.go(-1)
        },
        powPower(){
          layer.prompt({title: '请输入释放pow算力数量', formType: 0}, function(val, index){
            layer.close(index);
          });
        },
        
    },
    mounted () {

    } 
}
</script>

<style scoped>
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 62%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}

.top-bg-box{
  width: 100%;
  height: 10.5rem;
  background: url(../../assets/community/bg-images.png) no-repeat;
  background-size: 100% 100%;
  padding: 4rem 2rem 0 2rem;
}
.wallet-pow{
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  border-bottom: 1px solid #fff;
  margin-bottom: 0.5rem;
}
.wallet-pow p{
  color: #fff;
  font-size: 0.7rem;
}
.wallet-pow p span{
  margin-left: 0.5rem;
}
.power-pow{
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  border-bottom: 1px solid #fff;
  margin-bottom: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.power-pow p{
  color: #fff;
  font-size: 0.7rem;
}
.power-pow p span{
  margin-left: 0.5rem;
}
.power-pow button{
  width: 4rem;
  height: 1.5rem;
  line-height: 1.5rem;
  font-size: 0.6rem;
  color: #fff;
  border-radius: 0.3rem;
  outline: none;
  background-color: #ff6700;
}
</style>


