<template>
    <div class='content-box'>
  <div class="top-bar">
        <div class="account">
            <div>
                <router-link to="/login">
                    <span class="register">登录</span>
                </router-link>
                <router-link to="/forgetpassword">
                    <span class="sign-in">忘记密码</span>
                </router-link>
            </div>
        </div>
  </div>
  <div class="login-box">
      <img src="../../assets/login/login-logo.png" alt="" class="logo-nb">
      <div class='login-text-box'>
        <div>
          <img src="../../assets/login/id-number.png" alt="">
          <span>账号</span>
        </div>
        <input type="text" placeholder="请输入账号" v-model='mobile'>
        
        <div>
          <img src="../../assets/login/code-regx.png" alt="">
          <span>验证码</span>
        </div>
        <div class='code-box'>    
            <input type="text" placeholder="请输入验证码" v-model='code'>
            <p class="codetext"><img :src="codeImg" alt="" @click='changeImg'></p>
        </div>

        <div>
          <img src="../../assets/login/code-regx.png" alt="">
          <span>手机验证码</span>
        </div>
        <div class='code-box'>    
            <input type="text" placeholder="请输入验证码" v-model='codephone'>
            <p class="codetext" @click="gainCode" >{{ codeText }}</p>
        </div>

        <div>
          <img src="../../assets/login/pwd.png" alt="">
          <span>新密码</span>
        </div>
        <input type="password" placeholder="请输入新密码"  v-model='password'>

      </div>
      <button type="button" class="btn" @click='forgetPwd'>确  定</button>
    
  </div>
</div>
</template>

<script>
import axios from 'axios'
import md5 from 'js-md5'


export default {
    name: 'forgetpassword',
    data () {
        return {
            mobile:"",//账号
            codephone:'',//手机验证码
            code: '',   //图片验证码
            password:'',//新密码
            codeText: '发送验证码',
            codeClick:'true',
            allTime: 120,
            codeImg: register_img,
            loading: '',//loading动画
        }
    },
    methods: {
          gainCode () { //点击获取验证码
              let codeMobReg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/
              if (!this.mobile) {
                  this.$layer.msg('请输入手机号')
                  return false
              }else if (!codeMobReg.test(this.mobile)) {
                   this.$layer.msg('请输入正确的手机号码')
                  return false
              } else if (!this.code) {
                   this.$layer.msg('请输入图片验证码')
                  return false
              } else if (this.codeClick) {
                  this.gainCodeFun()
                  this.codeClick = false   
              }
          },
          changeImg(){
              this.codeImg = register_img + "?n=" + Math.random()*50
          },
          countdown () {  //倒计时    
            this.time = setInterval(this.countdownTime,1000)
            this.countdownTime()
          },
          countdownTime () {
              this.codeText = this.allTime + 's后获取'
              if (this.allTime < 0) {
                  clearInterval(this.time)
                  this.codeText = '发送验证码'
                  this.codeClick = true
                  this.allTime = 120
                  this.code=''
                  this.changeImg()
                  return false
              }
              this.allTime--
          },

          gainCodeFun () { //获取短信验证码
            const NewData = ({
                'mobile':this.mobile,
                'icode':this.code
            })
            this.$axios.post(forget_pwd_code, NewData)
                .then(this.gainCodeSucc)
                .catch(this.gainCodeError)
          },
          gainCodeSucc (res) {
              const data = res.data
              if (data.result == true) {
                this.$layer.msg('验证码发送成功')
                this.countdown()
            } else {
                this.codeClick = true
                this.$layer.msg(data.error.msg)
                this.code=''
                this.changeImg()
            }  
          },
          gainCodeError (error) {
              
          },

         forgetPwd(){  //忘记密码提交
            let codePwdReg = /(?!^(\d+|[a-zA-Z]+|[~!@#$%^&*?]+)$)^[\w~!@#$%^&*?]{6,18}$/
            let codeMobReg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/
            if (this.mobile == '') {
                this.$layer.msg('请输入手机号')
                return false
            } else if (!codeMobReg.test(this.mobile)) {
                this.$layer.msg('请输入正确的手机号')
                return false
            } else if (this.password == '') {
                this.$layer.msg('请输入密码')
                return false
            } else if (!codePwdReg.test(this.password)) {
                this.$layer.msg('请输入正确格式的密码')
                return false
            } else{
                const newData = ({
                  'mobile': this.mobile,
                  'password': this.password,
                  'sms':this.codephone
                })
                this.forgetFun(newData)
            }
          },
          forgetFun(newData){
            this.loading = layer.load(2, {
                shade: [0.2,'#000'] //0.1透明度的黑色背景
              })
            axios.post(forget_pwd, newData)
                .then(this.forgetSucc)
                .catch(this.forgetError)
          },
          forgetSucc (res) {
              const data = res.data
              if (data.error == null) {
                  console.log(data);
                  layer.close(this.loading)
                  this.$layer.msg('找回密码成功')
                  this.$router.push({name: 'login'})
              } else {
                  console.log(data);
                  layer.close(this.loading)
                  this.$layer.msg(data.error.msg)
              }
          },
          forgetError (error) {
            layer.close(this.loading)
            console.log('error');
            console.log(error);
          }

    },
    mounted () {

    } 
}
</script>

<style scoped>
  .content-box{
    min-height: 100vh;
  }
  .top-bar {
   /* position: fixed;
    top: 0;
    left: 0;*/
    z-index: 99;
    width: 100%;
    height: 3.5rem;
    padding: 0 1.067rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .logo-img {
    height: 1.067rem;
    float: left;
  }
  .account {
    width: 61%;
    height: 3.5rem;
  }
  .account a {
    color: #fff;
  }
  .sign-in {
    height: 3.5rem;
    color: #fff;
    line-height: 3.5rem;
    float: right;
    font-size: 1rem;
  }
  .register {
    height: 3.5rem;
    line-height: 3.5rem;
    color: #fff;
    float: left;
    font-size: 0.8rem;
  }
  .uid_box {
    color: #fff;
    text-align: right; 
  }
  .exit {
   padding-left: 5px;
   cursor: pointer; 
 }
 .login-box{
  width: 80%;
  height: auto;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 0.364rem;
  padding: 4rem 1rem 1rem 1rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform:translate(-50%,-50%)
}
.login-box .logo-nb{
  width: 3.63rem;
  height: 3.63rem;
  position: absolute;
  top: -1.8rem;
  left: 50%;
  margin-left: -1.8rem;
}
.login-text-box div{
  height: 1.4rem;
  line-height: 1.4rem;
  margin-bottom: 0.1rem;
}
.login-text-box img{
  vertical-align: top;
  height: 1.2rem;
}
.login-text-box span{
  color: #fff;
  font-size: 0.87rem;
  margin-left: 0.1rem;
}
.login-text-box input{
  width: 100%;
  height: 1.5rem;
  color: #fff;
  padding-left: 0.2rem;
  margin-bottom: 0.8rem;
  background: transparent;
  border-bottom: 1px solid #fff;
  outline: none;
}
.btn{
  display: block;
  width: 8.8rem;
  height: 2.2rem;
  background: #fff;
  border-radius: 0.2rem;
  font-size: 0.9rem;
  color: #000;
  margin: 0.2rem auto;
  line-height: 2.2rem;
  text-align: center;
  outline: none;
}
.forget-pwd{
  float: right;
}
.forget-pwd a{
  display: inline-block;
  color: #fff;
  font-size: 0.7rem;
  text-align: right;
  margin-top: 0.8rem;
}
.code-box{
    position: relative;
    margin-bottom: 0.8rem!important;
}
.codetext{
    position: absolute;
    top:0;
    right: 0.1rem;
    color: #fff;
    font-size: 0.5rem;
    line-height: 1.5rem;
}
</style>


