import Vue from 'vue'
import App from './App'
import router from './router'
import $ from 'jquery'
import axios from 'axios'
import qs from 'qs'
import store from './store'
import layer from 'vue-layer'
import clipboard from 'clipboard'
import ImgInputer from 'vue-img-inputer'
import 'vue-img-inputer/dist/index.css'

Vue.component('ImgInputer', ImgInputer)



axios.defaults.baseURL = 'http://www.hz20181008.cn';
axios.defaults.headers.post['Content-Type'] = 'application/json';
axios.defaults.withCredentials=true;
// axios.defaults.responseType = "JSON";

Vue.prototype.$axios = axios;
Vue.prototype.$qs = qs
Vue.prototype.$layer = layer(Vue)
Vue.prototype.clipboard = clipboard

Vue.config.productionTip = false

axios.defaults.transformResponse = [function(data){
    try {
        var res = $.parseJSON(data);
        return res;
    } catch (e) {
        return {
          result:null,
          error:{
            code:-1,
            msg:"network error"
          }
        };
    }
}];

router.beforeEach((to, from, next) => {
    // let nextRoute = ['home', 'orderrecord', 'community', 'buynode', 'wallet', 'purchasenode', 'turnout', 'moneyrecord', 'walletaddress',
    //     'activationcode', 'havenode', 'powpower', 'transferrecord', 'accountcenter', 'paysetup','banksetup','passwordsetup'
    // ]
    let nextRoute = ['login','register','forgetpassword']
    if (store.state.mobile) {
        next()
    } else if (!(nextRoute.indexOf(to.name) >= 0)) {
        router.push({name:'login'})
    } else {
        next()
    }
})

