<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'>返回</span>
                    <span class="sign-in">转让记录</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
         <div class='node-change'>
            <p v-for="(tab,index) in tabsName" @click="tabsSwitch(index)" v-bind:class="{active:tab.isActive}">{{ tab.name }}</p>
         </div>
         <div class='sale-node'>
           <p>当前拥有种子节点:<span>{{ nodeNum }}</span>个</p>
           <p>注：每次申购一个节点需要一个节点种子</p>
         </div>
         <div class='sale-node'>
           <p>当前可用激活码:<span>{{ activationNum }}</span>个</p>
           <p>注：新社区会员注册成功后需要激活才能正常使用</p>
         </div>
      </div>
    

      <div class='record-list'>

        <p v-if="nodeContentList == ''">您当前还没有节点记录</p>

        <div class='sell-content' v-else>
          <ul class='sell-title'>
            <li class='width-four'>状态</li>
            <li class='width-one'>数量</li>
            <li class='width-two'>时间</li>
            <li class='width-three'>账号</li>
          </ul>
          <ul class='sell-list' v-for='(item,index) in nodeContentList' :key='index'>
            <li class='width-four' v-if="item.type == 'trans'">转出</li>
            <li class='width-one'>{{ parseInt(item.num.substring(0,item.num.indexOf('.'))) }}</li>
            <li class='width-two'>{{ item.timestamp }}</li>
            <li class='width-three'>{{ item.mobile }}</li>
          </ul>
        </div>

      </div>

      <div class='record-list'>

        <p v-if="activationContent == ''">您当前还没有激活码记录</p>

        <div class='sell-content' v-else>
          <ul class='sell-title'>
            <li class='width-one2'>状态</li>
            <li class='width-two2'>账号</li>
            <li class='width-three2'>时间</li>
          </ul>
          <ul class='sell-list' v-for='(item,index) in activationContent' :key='index'>
            <li class='width-one2' v-if="item.type == 'trans'">转出</li>
            <li class='width-two2'>{{ item.mobile }}</li>
            <li class='width-three2'>{{ item.timestamp }}</li>

          </ul>
        </div>

      </div>
     

    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'transferRecord',
    component:{
    
    },
    data () {
        return {
          tabsName: [{
            name: "种子节点",
            isActive: true
          }, {
            name: "激活码",
            isActive: false
          }],
          active: false,
          nodeContentList:[],//节点记录内容列表
          activationContent:[],//激活码记录内容列表
          loading: '',//loading动画
          activationNum: '' , //激活码个数
          nodeNum: this.$store.state.nodeNum,//节点个数
        }
    },
    computed:{

    },
    methods: {
        goBack(){
          this.$router.go(-1)
        },
        getActivaNum(){ //获取激活码列表
          axios.post(wallet_activation)
          .then(this.getActivaNumSucc)
          .catch(this.getActivaNumError)
        },
        getActivaNumSucc(res){
          const data = res.data
          if(data.error == null){
            this.activationNum = data.result.length
          }else{
            this.$layer.msg(data.error.msg)
          }
        },
        getActivaNumError(error){
            layer.close(this.loading)
        },
        tabsSwitch: function(tabIndex) {
          var tabCardCollection = document.querySelectorAll(".sale-node"),
              recordList = document.querySelectorAll(".record-list"),
            len = tabCardCollection.length;
          for(var i = 0; i < len; i++) {
            tabCardCollection[i].style.display = "none";
            recordList[i].style.display = 'none';
            this.tabsName[i].isActive = false;
          }
          this.tabsName[tabIndex].isActive = true;
          tabCardCollection[tabIndex].style.display = "block";
          recordList[tabIndex].style.display = "block";
        },
        getActivation(){ //获取激活码转入转出记录
          axios.post(turn_in_out_activation)
          .then(this.getActivationSucc)
          .catch(this.getActivationError)
        },
        getActivationSucc(res){
          const data = res.data
          if(data.error == null){
            this.activationContent = data.result
          }
        },
        getActivationError(error){

        },
        getNode(){ //获取节点转入转出记录
          this.loading = layer.load(2, {
             shade: [0.2,'#000'],//0.1透明度的黑色背景
              time: 10*1000
          })
          axios.post(turn_in_out_node)
          .then(this.getNodeSucc)
          .catch(this.getNodeError)
        },
        getNodeSucc(res){
          const data = res.data
          if(data.error == null){
            this.nodeContentList = data.result
            layer.close(this.loading)
          }else{
            layer.close(this.loading)
            this.$layer.msg(data.error.msg)
          }
        },
        getNodeError(error){

        },
    },
    mounted () {
      this.getActivaNum()
      this.getActivation()
      this.getNode()
    } 
}
</script>

<style scoped>
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 60%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}

.top-bg-box{
  width: 100%;
  height: 13.6rem;
  background: url(../../assets/home/bg-images.png) no-repeat;
  background-size: 100% 100%;
  padding-top: 4rem;
}
.node-change{
  width: 100%;
  height: 2.5rem;
  padding: 0 3.5rem;
  background: #fff;
  z-index: 33;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.node-change p{
  width: 35%;
  height: 1.5rem;
  text-align: center;
  color: #000;
  font-size: 0.74rem;
  position: relative;
  
}
.node-change p:first-child:after{
  content: '';
  width: 1px;
  height: 1.5rem;
  background-color: #d0cfcf;
  position:absolute; 
  top: -0.2rem;
  right: -2rem;
}
.active{
  color: #1075df!important;
  border-bottom: 1px solid #1075df;
}
.sale-node{
  width: 100%;
  padding: 1.5rem 2rem 0 2rem;
}
.sale-node ~ .sale-node{
  display: none;
}
.sale-node p{
  color: #fff;
  font-size: 0.7rem;
  line-height: 1.5rem;
}
.sale-node p span{
  font-size: 1.2rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}
.sale-node p:last-child{
  color: #fff;
  font-size: 0.6rem;
  line-height: 1rem;
  margin-top: 1rem;
}
.record-list ~ .record-list{
  display: none;
}
.record-list{
  width: 100%;
  height: auto;
  background: #fff;
  margin-top: 1rem;
  padding: 1rem 0;
  margin-bottom: 1rem;
}
.record-list p{
  color: red;
  font-size: 0.7rem;
  text-align: center;
}
.sell-content{
  width: 100%;
  height: auto;
  background: #fff;
}
.sell-title{
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  font-size: 0.7rem;
  color: #000;
  text-align: center;
  border-bottom: 1px solid #ccc;
}
.sell-title li{
  float: left;
}
.width-one{
  width: 20%;
}
.width-two{
  width: 35%;
}
.width-three{
  width: 25%;
}
.width-four{
  width: 20%;
}
.width-one2{
  width: 25%;
}
.width-two2{
  width: 35%;
}
.width-three2{
  width: 40%;
}
.sell-list{
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  font-size: 0.6rem;
  color: #000;
  text-align: center;
  border-bottom: 1px solid #ccc;
}
.sell-list li{
  float: left;
}
</style>


