<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'>返回</span>
                    <span class="sign-in">转出 N B</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
          <div class='rule-box'>
            <p>规则:</p>
            <p>NB只能在本平台的钱包之间互相转出，暂不能转到其他平台的钱包</p>
          </div>
      </div>

      <div class='buy-effect'>
        <div class='buy-list'>
          <span>当前拥有NB：</span>
          <span>{{ haveNode }}</span>   
        </div>
        <div class='buy-effect-list'>
          <p>对方地址：</p>
          <input type="text" name='' placeholder="请输入对方地址" v-model='otherAddress'>
        </div>
        <div class='buy-list'>
          <span>转出数量：</span>
          <input type="number" name='' placeholder="请输入转出数量" v-model='turnNum'>      
        </div>
        <div class='buy-list'>
          <span>二级密码：</span>
          <input type="password" name=''placeholder="请输入二级密码" v-model='password'>      
        </div>

        <button @click='turnOutbtn'>转  出</button>
        <button @click='goRecord'>资金记录</button>
      </div>

    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'wallet',
    component:{
    
    },
    data () {
        return {
           haveNode:'132',//拥有NB数量
           otherAddress:'',//转出地址
           turnNum:'',//转出数量
           password:'',//二级密码
        }
    },
    methods: {
        goBack(){
          this.$router.go(-1)
        },
        turnOutbtn(){ //转出按钮
          if(this.otherAddress == ''){
            this.$layer.msg('请输入对方地址')
          } else if (this.turnNum == ''){
            this.$layer.msg('请输入转出数量')
          } else if (this.password == ''){
            this.$layer.msg('请输入二级密码')
          } else {
            const newData = ({
              number: this.turnNum,
              address: this.otherAddress,
              password: this.password
            })
            this.goTurnOut(newData)
          }
        },
        goTurnOut(newData){
          axios.post(turn_out_node,newData)
          .then(this.goTurnOutSucc)
          .catch(this.goTurnOutError)
        },
        goTurnOutSucc(res){
          const data = res.data
          if(data.error == null){
            this.$layer.msg('转出成功')
          } else {
            this.$layer.msg(data.error.msg)
          }
        },
        goTurnOutError(error){

        },
        goRecord(){  //资金记录按钮
          this.$router.push('/moneyrecord')
        }
    },
    mounted () {

    } 
}
</script>

<style scoped>
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 60%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}

.top-bg-box{
  width: 100%;
  height: 13.6rem;
  background: url(../../assets/home/bg-images.png) no-repeat;
  background-size: 100% 100%;
  padding-top: 5rem;
  position: relative;
}
.rule-box{
  width: 15.727rem;
  height: 6.745rem;
  padding: 2rem 2.4rem;
  background-image: url('../../assets/wallet/line-rule.png');
  background-size: cover;  
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 0.5rem;
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%,-50%);
}
.rule-box p{
  color: #fff;
  font-size: 0.7rem;
}

.buy-effect{
  width: 100%;
  height: auto;
  padding: 1rem 2rem;
  margin-top: 1rem;
  background: #fff;
}
.buy-effect-list{
  line-height: 2rem;
  color: #000;
  font-size: 0.7rem;
  border-bottom: 1px solid #E5E5E5;
}
.buy-effect-list p{
  margin-right: 0.5rem;
}
.buy-effect-list input{
  width: 100%;
  outline: none;
  display: block;
}
.buy-effect-list input::placeholder{
  color: #ccc;
}
.buy-list{
  height: 2rem;
  line-height: 2rem;
  color: #000;
  font-size: 0.7rem;
  border-bottom: 1px solid #E5E5E5;
}
.buy-list input{
  width: 7rem;
  outline: none;
  border:none;
  height: 2rem;
  border-bottom: 1px solid #E5E5E5;
}
.buy-list input::placeholder{
  color: #ccc;
}
.buy-list span{
  margin-right: 0.5rem;
}
.buy-effect button {
  display: block;
  width: 12.364rem;
  height: 1.818rem;
  background-color: #1075df;
  border-radius: 0.182rem;
  color: #fff;
  font-size: 0.8rem;
  line-height: 1.818rem;
  text-align: center;
  margin: 1rem auto;
  outline: none;
}
.buy-effect button:last-child{
  background-color: #F76F3F;
}
</style>


