<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'>返回</span>
                    <span class="sign-in">节点种子转让</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
          <div class='other-name'>
            <span>转出数量</span>
            <input type="number" placeholder='请输入转出数量' v-model='num'>
          </div>
          <div class='other-name'>
            <span>对方账户</span>
            <input type="text" placeholder='请输入对方账户' v-model='mobile'>
          </div>
          <button @click='turnOut'>转  让</button>    
      </div>

     

    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'turnOutNode',
    component:{
    
    },
    data () {
        return {
           num:'',//转出数量
           mobile:'',//转入账号
           loading: '',//loading动画

        }
    },
    methods: {
       goBack(){
         this.$router.go(-1)
       },
       turnOut(){
          if(this.num == ''){
            this.$layer.msg('请输入转出数量')
          }else if(this.mobile == ''){
            this.$layer.msg('请输入转入账号')
          }else{
            const newData = ({
            assets1: this.num,
            mobile: this.mobile
          })
            this.turnOutBtn(newData)
          }  
       },
       turnOutBtn(newData){
        this.loading = layer.load(2, {
                shade: [0.2,'#000',],//0.1透明度的黑色背景
                time: 3*1000
              })
        axios.post(wallet_turnout_node,newData)
        .then(this.turnOutBtnSucc)
        .catch(this.turnOutBtnError)
       },
       turnOutBtnSucc(res){
         const data = res.data
         if(data.error == null){
          layer.close(this.loading)
          this.$layer.msg('转出成功')
          this.num = ''
          this.mobile = ''
          this.$router.push({name:'wallet'})
         }else{
          layer.close(this.loading)
          this.num = ''
          this.mobile = ''
          this.$layer.msg(data.error.msg)
         }
       },
       turnOutBtnError(error){
          layer.close(this.loading)
       }

    },
    mounted () {

    } 
}
</script>

<style scoped>
.top-bar {
  background: url(../../assets/wallet/nav-img.png) no-repeat;
  background-size: 100% 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 68%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}
.top-bar{

}
.top-bg-box{
  width: 100%;
  padding: 5rem 2rem 0rem 2rem;

}
.other-name{
  width: 16rem;
  height: auto;
  margin: 0.5rem auto;
  background: #fff;
  padding-left: 1rem;
  border-radius: 0.3rem;
  font-size: 0.6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.other-name input{
  width: 11rem;
  height: 2rem;
  border: none;
  outline: none;
  border-radius: 0.3rem;
}
.top-bg-box button{
  width: 6rem;
  height: 2rem;
  background: #1074E3;
  border-radius: 0.3rem;
  outline: none;
  display: block;
  margin: 1rem auto;
  color: #fff;
  font-size: 0.7rem;
}
</style>


