<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'>返回</span>
                    <span class="sign-in">我的社区</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
          <img src="../assets/community/user-logo.png" alt="">
          <!-- <p style="color:#fff;">{{ queryid }}</p> -->
          <div class='user-name'>
            <p>{{ username }}</p>
            <p>当前社区人数：<span>{{ peopleNum }}</span>人</p>
          </div>
          <div class="user-vip">
            <img src="../assets/community/logo-vip.png" alt="">
            <span>VIP&nbsp;<span>{{ vipGrade }}</span></span>
          </div>
      </div> 

      <div class='level-content'>
          <p>当前位置</p>
          <div class='queue-content'>
            <div @click='myCommunityList'>我的社区</div>
            <ul class='queue-box'>
              <li  v-for='(items,index) in underUserList' :key='index'  @click='sliceMobile(items,index)'>{{ items }}</li>
            </ul>  
          </div> 
      </div>

      <div class='level-list-box'>
        <ul class='level-title'>
          <li>手机号</li>
          <li>状态</li>
          <li>等级</li>
        </ul>
        <ul class='level-list' v-for="(item,index) in userList" :key='index'>
          <li @click='addMobile(item.mobile)'>{{ item.mobile }}</li>
          <li v-if="item.isActive">已激活</li>
          <li v-else >未激活</li>
          <li>VIP&nbsp;<span>{{ item.level }}</span></li>
        </ul>
        
      </div>

    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'community',
    component:{
    
    },
    data () {
        return {
          username: this.$store.state.nickname, //用户昵称
          peopleNum: '',  //社区人数
          vipGrade: this.$store.state.vipGrade,  // vip等级
          mobile: this.$store.state.mobile, //用户登录手机号 
          userList:[],//社区用户列表 
          underUserList:[],// 我的下级用户列表
          loading: '',//loading动画

        }
    },
    methods: {
        goBack(){
          this.$router.go(-1)
        },
        myCommunityList(){
          this.getContent(this.$store.state.mobile)
          this.underUserList = []
        },
        addMobile(datas){
          this.underUserList.push(datas)
          this.getContent(datas)
        },
        sliceMobile(datas,index){
          console.log(datas)
          this.getContent(datas)
          this.underUserList = this.underUserList.slice(0,index+1)
        },
        getContent(usermobile){
          const newData = ({
            mobile: usermobile ? usermobile : this.mobile
          })
          this.loading = layer.load(2, {
                shade: [0.2,'#000',],//0.1透明度的黑色背景
                time: 3*1000
              })
          axios.post(my_community,newData)
          .then(this.getContentSucc)
          .catch(this.getContentError)
        },
        getContentSucc(res){
          const data = res.data 
          // console.log(data)
          if(data.error == null){
            this.userList = data.result,
            this.peopleNum = data.result.length
            layer.close(this.loading)
            if(data.result == ''){
              this.$layer.msg('暂无下级')
            }
          }else{
            layer.close(this.loading)
            this.$layer.msg(data.error.msg)
          }
        },
        getContentError(error){

        }
    },
    mounted () {
        this.getContent()
    } 
}
</script>

<style scoped>
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 62%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}

.top-bg-box{
  width: 100%;
  height: 10.5rem;
  background: url(../assets/community/bg-images.png) no-repeat;
  background-size: 100% 100%;
  padding: 3rem 2rem 0 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;

}
.top-bg-box img{
  height: 4.5rem;
}
.user-name p:first-child{
  color: #fff;
  font-size: 0.8rem;
}
.user-name p:last-child{
  color: #fff;
  font-size: 0.7rem;
  margin-top: 0.8rem;
}
.user-vip{
  line-height: 1.3rem;
}
.user-vip img{
  vertical-align: bottom;
  height: 1.3rem; 
}
.user-vip span{
  color: #fff;
  font-size: 0.7rem; 
}

.level-content{
  width: 100%;
  margin-top: 1rem;
  background: #fff;
  padding: 1rem;
}
.level-content p{
  color: #000;
  font-size: 0.7rem;
  margin-bottom: 0.5rem;
}
.queue-content{
  width: 100%;
  min-height: 2rem;
  max-height: 5rem;
  overflow-y: scroll;
  margin-bottom: 0.5rem;
}
.queue-content div{
  width: 2.5rem;
  height: 1.5rem;
  line-height: 2rem;
  color: #000;
  float: left;
  text-align: right;
}
.queue-box{
  min-height: 2rem;
  max-height: 5rem;
}

.queue-box li{
  width: 4.5rem;
  height: 1.5rem;
  line-height: 2rem;
  color: #000;
  float: left;
  text-align: right;
  position: relative;
}
.queue-box li:before{
  content:'';
  display: block;
  width: 0.35rem;
  height: 0.35rem;
  border-right: 1px solid #FD6532;
  border-top: 1px solid #FD6532;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  position: absolute;
  top: 0.75rem;
  left: 0.15rem;
}
.level-list-box{
  width: 100%;
  margin-top: 1rem;
  background: #fff;
  padding: 1rem;
  margin-bottom: 2rem;
}
.level-title{
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  border-bottom: 1px solid #ccc;
}
.level-title li{
  width: 33%;
  float: left;
  text-align: center;
  color: #000;
  font-size: 0.6rem;
}
.level-list{
  width: 100%;
  height: 2rem;
  line-height: 2rem;
  border-bottom: 1px solid #ccc;
}
.level-list li{
  width: 33%;
  float: left;
  text-align: center;
  color: #000;
  font-size: 0.6rem;
}
</style>


