<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'>返回</span>
                    <span class="sign-in">账户信息管理</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
          <router-link to="/paysetup">
            <div class="account-list">
              <img src="../../assets/wallet/pay-ali.png" alt="">
              <p>支付宝</p>
              <p></p>
            </div>   
          </router-link>
          <router-link to="/wechatsetup">
            <div class="account-list">
              <img src="../../assets/wallet/wechat.png" alt="">
              <p>微信</p>
              <p></p>
            </div>   
          </router-link>
          <router-link to="/banksetup">
            <div class="account-list">
              <img src="../../assets/wallet/bankcard.png" alt="">
              <p>银行卡</p>
              <p></p>
            </div>   
          </router-link>
          <router-link to="/loginpwdsetup">
            <div class="account-list">
              <img src="../../assets/wallet/password.png" alt="">
              <p>修改登录密码</p>
              <p></p>
            </div>   
          </router-link>
          <router-link to="/passwordsetup">
            <div class="account-list">
              <img src="../../assets/wallet/password.png" alt="">
              <p>修改二级密码</p>
              <p></p>
            </div>   
          </router-link>
      </div>

     

    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'haveNode',
    component:{
    
    },
    data () {
        return {
           
        }
    },
    methods: {
        goBack(){
          this.$router.go(-1)
        },
    },
    mounted () {

    } 
}
</script>

<style scoped>
.top-bar {
  background: url(../../assets/wallet/nav-img.png) no-repeat;
  background-size: 100% 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 67%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}
.top-bar{

}
.top-bg-box{
  width: 100%;
  padding: 5rem 0.5rem 0rem 0.5rem;
}

.account-list{
  width: 100%;
  height: 3rem;
  border-radius: 0.5rem;
  background-color: #fff;
  margin-bottom: 0.5rem;
  padding-left: 0.5rem;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.account-list img{
  height: 1.5rem;
}
.account-list p:nth-child(2){
  width: 12rem;
  color: #828282;
  font-size: 0.8rem;
  text-align: left;
}
.account-list p:last-child:after {
  content: '';
  display: block;
  width: 13px;
  height: 13px;
  border-right: 1px solid #828282;
  border-top: 1px solid #828282;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  position: absolute;
  top: 1.3rem;
  right: 1rem;
}
</style>


