<template>
    <div>
      <div class="top-bar">
            <div class="account">
                <div>
                    <span class="register" @click='goBack'>返回</span>
                    <span class="sign-in">购   买</span>
                </div>
            </div>
      </div>

      <div class='top-bg-box'>
         <p>当前拥有:</p>
         <div class='price-number'>
              <span>{{ haveNumber }}</span>
              <span>ETH</span>
              <span>≈</span>
              <span>{{ havePriceRmb }}</span>
              <span>CNY</span>
         </div>
         <p>注：ETH当前价格<span>{{ priceETH }}</span></p>    
      </div> 

      <div class='buy-effect'>
        <div class='buy-effect-list'>
          <span>购买类型：</span>
          <input type="radio" name='type-buy' value="11" checked="checked" v-model='picked'>激活码
          <input type="radio" name='type-buy' value="22" v-model='picked'>节点种子
        </div>
        <div class='buy-list'>
          <span>购买数量：</span>
          <input type="number" name='' placeholder="请输入购买数量" v-model='buyNum'>      
        </div>
        <div class='buy-list'>
          <span>需要消耗ETH：</span>
          <!-- <input type="number" name='' :value='consumeNum'>    -->
          <span>{{ consumeNum }}</span>
          <span>ETH</span>   
        </div>
        <div class='buy-list'>
          <span>二级密码：</span>
          <input type="password" name=''placeholder="请输入二级密码" v-model='password'>      
        </div>

        <button @click='buyNodeBtn'>购买</button>
      </div>

    </div>
</template>

<script>
import axios from 'axios'
export default {
    name: 'buynode',
    component:{
    
    },
    data () {
        return {
          haveNumber:'888.88',// 拥有ETH数量
          havePriceRmb: '',// CNY价格
          priceETH:'6666.66', // ETH当前价格
          buyNum:'',//购买数量
          priceNode:'12.3',//节点价格
          consumeNum:'0',//消耗ETH数量
          picked: 11,//支付方式
          password:'',//二级密码
        }
    },
    updated() {
          let consumeNumAll = this.buyNum * this.priceNode; 
          this.consumeNum = consumeNumAll > 0.01 ? consumeNumAll.toFixed(4) : consumeNumAll.toFixed(6);
    },
    methods: {
        goBack(){
          this.$router.go(-1)
        },
        getEthContent(){  //获取用户拥有ETH数量
          axios.post(buy_node_price,{queryid:this.$route.query.name})
          .then(function(res){
            const data = res.data
            if(data.error == null){
              this.haveNumber = data.result.haveNumber,
              this.priceETH = data.result.priceETH,
              this.havePrice()
            }
          })
        },
        havePrice(){  //换算拥有CNY数量
          let havePriceAll = this.haveNumber * this.priceETH;
          this.havePriceRmb = havePriceAll > 0.01 ? havePriceAll.toFixed(2) : havePriceAll.toFixed(4);
        },
        buyNodeBtn(){  //购买节点提交
          if(this.buyNum == '' || this.buyNum == 0 ){
            this.$layer.msg('请输入正确的购买数量')
          } else if (this.password == ''){
            this.$layer.msg('请输入二级密码')
          } else {
            const newData = ({
              picked:this.picked,
              number:this.buyNum
            })
            this.buyNode(newData)
          }
        },
        buyNode(newData){
          axios.post(buy_node,newData)
          .then(this.buyNodeSucc)
          .catch(this.buyNodeError)
        },
        buyNodeSucc(res){
          const data = res.data
          if(data.error == null){
            this.$layer.msg('购买成功')
            this.$router.push({name:'home'})
          } else {
            this.$layer.msg(data.error.msg)
          }
        },
        buyNodeError(error){

        }
    },
    mounted () {
        this.getEthContent()
    } 
}
</script>

<style scoped>
.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 3.5rem;
  padding: 0 1.067rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo-img {
  height: 1.067rem;
  float: left;
}
.account {
  width: 57%;
  height: 3.5rem;
}
.account a {
  color: #fff;
}
.sign-in {
  height: 3.5rem;
  color: #fff;
  line-height: 3.5rem;
  float: right;
  font-size: 1rem;
}
.register {
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  float: left;
  font-size: 0.8rem;
  padding-left: 0.8rem;
  position: relative;
}
.register:after {
  content: '';
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  border-right: 1px solid #fff;
  border-top: 1px solid #fff;
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  position: absolute;
  top: 1.45rem;
  left: 0.1rem;
}

.top-bg-box{
  width: 100%;
  height: 13.6rem;
  background: url(../assets/buynode/buy-bgimg.png) no-repeat;
  background-size: 100% 100%;
  padding: 5rem 2rem 0 2rem;
  color: #fff;
}
.top-bg-box p:first-child{
  font-size: 0.8rem;
  margin-bottom: 1rem;
}

.top-bg-box p:last-child{
  font-size: 0.6rem;
}
.price-number{
  font-size: 1rem;
  text-align: center;
  margin-bottom: 1rem;
}


.buy-effect{
  width: 100%;
  height: auto;
  padding: 1rem 2rem;
  margin-top: 1rem;
  background: #fff;
}
.buy-effect-list{
  height: 2rem;
  line-height: 2rem;
  color: #000;
  font-size: 0.7rem;
  border-bottom: 1px solid #E5E5E5;
}
.buy-effect-list span{
  margin-right: 0.5rem;
}
.buy-list{
  height: 2rem;
  line-height: 2rem;
  color: #000;
  font-size: 0.7rem;
  border-bottom: 1px solid #E5E5E5;
}
.buy-list input{
  outline: none;
  border:none;
  height: 2rem;
  border-bottom: 1px solid #E5E5E5;
}
.buy-list input::placeholder{
  color: #ccc;
}
.buy-list span{
  margin-right: 0.5rem;
}
.buy-effect button {
  display: block;
  width: 12.364rem;
  height: 1.818rem;
  background-color: #1075df;
  border-radius: 0.182rem;
  color: #fff;
  font-size: 0.8rem;
  line-height: 1.818rem;
  text-align: center;
  margin: 1rem auto;
  outline: none;
}
</style>


