import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)


let intUid = ''
if (localStorage.getItem('uid')) {
    intUid = localStorage.getItem('uid')
}

let mob = ''
if (sessionStorage.getItem('mobiles')) {
    mob = sessionStorage.getItem('mobiles')
}
let name = ''
if (sessionStorage.getItem('username')) {
    name = sessionStorage.getItem('username')
}
let vip = ''
if (sessionStorage.getItem('vipGrade')) {
    vip = sessionStorage.getItem('vipGrade')
}
let invit = ''
if (sessionStorage.getItem('invitcode')){
    invit = sessionStorage.getItem('invitcode')
}
let node = ''
if (sessionStorage.getItem('nodeNum')){
    node = sessionStorage.getItem('nodeNum')
}
export default new Vuex.Store({
    state: {
        userId: intUid,
        mobile: mob,
        nickname: name,
        vipGrade: vip,
        invitcode: invit,
        nodeNum: node
    },
    mutations: {
        AddUid(state, uid) {
            state.userId = uid
            localStorage.setItem('uid', uid)
        },
        clearUid(state) {
            state.userId = ''
            state.mobile = ''
            sessionStorage.clear()
        },
        AddMob(state,mobile) {
            state.mobile = mobile
            sessionStorage.setItem('mobiles', mobile)
        }
        ,
        AddName(state,nickname) {
            state.nickname = nickname
            sessionStorage.setItem('username', nickname)
        }
        ,
        AddVip(state,vipGrade) {
            state.vipGrade = vipGrade
            sessionStorage.setItem('vipGrade', vipGrade)
        },
        AddInvit(state,invitcode){
            state.invitcode = invitcode
            sessionStorage.setItem('invitcode', invitcode)
        },
        AddNode(state,nodeNum){
            state.nodeNum = nodeNum
            sessionStorage.setItem('nodeNum', nodeNum)
        }

    }
})


